.chat {
    background-color: #fff;
    overflow: hidden;
    @include flexColumn;

    &__body {
        padding: 0 25px;
        @include bb-scroll--thin;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 70px;
    } //.chat__body

    &__footer {
        background-color: $color-bg--lightest;
        @include border(top);
        @include flexCenter;
        justify-content: center;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
    } //.chat__footer

    &__back {
        display: inline-block;
        padding: 0 25px 0 35px;
        line-height: 50px;
        font-family: $f-light;
        font-size: 15px;
        color: $gray--light;
        cursor: pointer;
        @include tr;
        @include flexCenter;
        align-items: center;

        &:hover {
            color: $gray;
            @include gp(to right, #eaeaea, #fff);
        }

        i {
            @include vy;
            left: 15px;
            margin-right: 7px;
            font-size: 27px;
            line-height: 1;
            color: $gray--light;
        }
        @include respond-to(xs) {

            span {
                display: none;
            }
        }
    } //.chat__back

    &__user {
        text-align: center;
        @include vcenter;

        &-name {
            display: block;
            margin-bottom: 5px;
            font-size: 14px;
            line-height: 1;
            color: $gray--darkest;
        }

        &-status {
            display: block;
            @extend %textLight;
            font-size: 12px;
            line-height: 1;
            color: $gray--light;
        }
    } //.chat__user

    &__form {
        width: 100%;
        margin-top: auto;
        padding: 15px 20px;
        display: flex;
        align-items: center;
        justify-content: space-around;

        textarea {
            width: 80%;
            height: 35px;
            min-height: initial;
            max-height: 103px;
            padding: 8px 10px 5px;
            font-size: 13px;
            color: $gray--darkest;
            border: 1px solid $color-border--lighten;
            border-collapse: collapse;
            border-radius: $border-radius;
            resize: none;
            @include bb-scroll--thin;

            &:focus {
                font-size: 13px;
            }
            @include placeholder {
                font-size: 13px;
            }
        }
    } //.chat__form

    &__buttons {
        height: 55px;
        text-align: center;
        @include flexCenter;
        justify-content: center;
        @include respond-to(md) {
            width: 100%;
            text-align: center;
        }
        @include respond-to(xs) {
            height: auto;

            .button {
                padding: 4px 20px 5px;
                font-size: 15px;
            }
        }

        .button {
            width: 150px;
            height: 32px;
            padding: 0;
            text-align: center;
            font-size: 14px;
            line-height: 1;
            border-radius: 0;
            @include respond-to(xs) {
                width: 50%;
            }
        }
    } //.chat__buttons

    &__btn {
        display: inline-block;
        width: 40px;
        height: 40px;
        padding: 0;
        text-align: center;
        border: 1px solid $color-border--lighten;
        border-radius: $border-radius;
        box-shadow: none;
        background-color: transparent;
        cursor: pointer;
        @include tr;

        &:hover,
        &:focus {
            border-color: $gray--light;
            box-shadow: none;
            background-color: transparent;

            &::before {
                color: $gray--light;
            }
        }

        &::before {
            content: '\f1d8';
            @include fa('solid');
            font-size: 20px;
            color: $gray--light;
            position: relative;
            top: 4px;
            @include tr;
        }

        &_list {
            display: none;
            width: 38px;
            height: 38px;
            margin-left: 15px;
            margin-right: 15px;

            &::before {
                content: '\f27a';
                @include fa('solid');
                font-size: 20px;
                color: $gray--light;
                position: relative;
                top: -8px;
                @include tr;
            }
            @include max-screen(1024) {
                display: inline-block;
            }
            @include max-screen(375) {
                width: 32px;
                height: 32px;

                &::before {
                    font-size: 18px;
                    top: -10px;
                }
            }
        }

        input {
            display: none;
        }
    } //.chat__btn

    &__order {
        padding: 12px 15px;
        background-color: #fff;
        border-bottom: 1px solid $color-border--lightest;
        position: relative;
    } //.chat__order

    &__unread {
        display: none;
        width: 6px;
        height: 6px;
        margin-left: 10px;
        border-radius: 50%;
        background-color: $color-main;
    } //.chat__unread

    &__empty {
        height: 100%;
        font-size: 14px;
    } //.chat__empty
} //.chat

.message {
    padding: 15px 0 20px;
    position: relative;
    @include flexColumn;

    &.is-income {

        .message__dialog {
            background-color: #a6cef1;
            box-shadow: 1px 1px 15px #a7a7a7;

            &-item {
                color: #fff;
            }
        }
    }

    &.is-confirm {

        .message__dialog {
            background-color: $color-success;
            box-shadow: 1px 1px 15px #a7a7a7;

            &-item {
                color: #fff;
            }
        }
    }

    &.is-reject {

        .message__dialog {
            background-color: $color-danger--light;
            box-shadow: 1px 1px 15px #a7a7a7;

            &-item {
                color: #fff;
            }
        }
    }

    &__inner {
        margin-bottom: 20px;
        position: relative;
        display: flex;

        &:last-child {
            margin-bottom: 0;
        }
    } //.message__item

    &__text {

        &--top {
            margin-bottom: 5px;
            @include flexCenter;
        }
    } //.message__text

    &__author {
        font-family: $f-bold;
        font-size: 14px;
        line-height: 1;
        color: $gray--darken;
    } //.message__author

    &__time {
        margin-left: 7px;
        @extend %textLight;
        font-size: 13px;
        line-height: 1;
        color: $gray--light;
    } //.message__time

    &__dialog {
        padding: 10px 15px;
        font-family: $f;
        border-radius: 0 8px 8px;
        box-shadow: 1px 1px 15px #ddd;
        background-color: #fff;

        &-item {
            margin-bottom: 10px;
            font-size: 13px;
            color: #000;

            &:last-child {
                margin-bottom: 0;
            }
        }
    } //.message__dialog

    .user {

        &__img {
            width: 40px;
            height: 40px;
        }
    }
} //.message
