.steps {
    @include respond-to(xs) {
        padding: 0 10px;
    }

    &__list {
        @include flexColumn;
    } //.steps__list

    &__item {
        position: relative;
        z-index: z(default);

        &.is-active {
            color: #000;
            background-color: $color-bg--lighten;

            &::before {
                content: '';
                display: block;
                height: 100%;
                width: 2px;
                background-color: #000;
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1;
            }

            .steps__link {
                color: #000;
            }
        }

        &.is-checked {

            .steps__link {

                &::before {
                    content: '\f058';
                    color: $color-success;
                }
            }
        }

        &.is-invalid {

            .steps__link {

                &::before {
                    content: '\f06a';
                    color: $color-danger;
                }
            }
        }
    } //.steps__item

    &__link {
        display: block;
        padding: 7px 10px;
        font-family: $f-medium;
        font-size: 15px;
        color: $color-text;
        transition: initial;

        &::before {
            content: '';
            display: block;
            @include fa('light');
            font-size: 18px;
            @include vy;
            right: 10px;
        }

        &:hover {
            color: #000;
            background-color: $color-bg--lighten;
        }
    } //.steps__link

    &__search {
        padding: $gutter;
        border-bottom: 1px solid $color-border--lightest;

        .search {
            max-width: 100%;
            width: 100%;
            margin: 0;

            &__group {
                margin-right: $gutter;
                flex: 1;
            }

            &__input,
            &__btn {
                background-color: $color-bg--light;
            }

            &__input {
                border: 0;
                border-radius: $border-radius;
            }

            &__btn {
                padding: 0 15px;
            }

            &__icon {
                display: block;
                fill: $color-icon;
            }
        }
    } //.steps__search

    &__back {
        outline: none;

        &:hover {
            text-decoration: none;
        }
        @include respond-to(xs) {
            padding: 10px 0 10px 10px;
        }

        i {
            margin-right: 0;
            font-size: 18px;
        }
    } //.steps__back

    .profile-editor {
        @include respond-to(xs) {
            margin: 0 -10px 20px;
        }

        &__form {
            @include col();
            @include size(9);
            @include size-md(8);
            @include size-sm(12);

            @include respond-to(sm) {
                order: 2;
            }

            @include respond-to(xs) {
                box-shadow: none;
                border-bottom: 1px solid $color-border--lightest;
            }

            &--small {

                .profile-editor {

                    &__inner {
                        max-width: 768px;
                        margin: auto;
                    }
                }
            }
        } //.steps .profile-editor__form

        &__info {
            @include respond-to(xs) {
                border-top: 1px solid $color-border--lightest;
                border-bottom: 1px solid $color-border--lightest;
                border-radius: 0;
                box-shadow: none;
            }
        } //.steps .profile-editor__info

        &__upload {
            padding: 15px 10px 0;
        } //.steps .profile-editor__upload
    } //.steps .profile-editor
} //.steps

.working-hours {

    &__wrap {
        width: 100%;
        padding-top: 11px;

        .bb-checkbox--slide {
            margin-bottom: 15px;

            .bb-checkbox {

                &__title {
                    margin-left: 10px;
                    font-size: 14px;
                    line-height: 1;
                }
            }
        }
    } //.working-hours__wrap

    &__inner {
        @include flexColumn;
        align-items: center;
    } //.working-hours__inner

    &__item {
        margin-bottom: 10px;
        position: relative;
        @include flexCenter;

        &:first-child {

            .working-hours__btn--dell {
                display: none;
            }
        }

        &:last-child {
            margin-bottom: 0;
        }
    } //.working-hours__item

    &__day {
        margin-right: 10px;
    } //.working-hours__day

    &__time {
        @include flexCenter;
        flex-grow: 1;

        &-item {
            min-width: 123px;
            width: 100%;
        }
    } //.working-hours__time

    &__devider {
        margin: 0 5px;
    } //.working-hours__devider

    &__btn {

        &--add {
            width: auto;
            padding: 3px 25px 4px;
            font-family: $f-light;
            font-size: 14px;
            background-color: transparent;
            border: 1px solid $color-border--light;
            border-radius: $border-radius;
            outline: none;

            &:hover {
                color: $gray--darkest;
                border-color: $color-border;
            }
        }

        &--dell {
            background-color: transparent;
            border: none;
            cursor: pointer;
            outline: none;
            @include vy;
            right: -32px;

            .icon {
                width: 16px;
                height: 16px;
                fill: $gray;
            }
        }
    } //.working-hours__btn

    .bb-checkbox {
        width: 40px;
        margin-right: 0;

        @include respond-to(sm) {
            width: 50px;
        }

        &__wrap {
            margin-bottom: 15px;

            .bb-checkbox--small {

                &:first-child {
                    border-radius: $border-radius 0 0 $border-radius;
                }

                &:last-child {
                    border-radius: 0 $border-radius $border-radius 0;
                }
            }
        }

        &__title {
            font-size: 13px;
        }
    } //.working-hours .bb-checkbox

    .bb-input {

        &__wrap {
            margin-bottom: 15px;
        }

        &__input {
            border: 1px solid $color-border--light;
        }

        &--time {
            width: 101px;

            & + .bb-input--time {
                margin-left: 30px;
            }
        }
    } //.working-hours .bb-input
} //.working-hours

.category-item {
    min-height: 170px;
    margin-bottom: $gutter;
    padding-top: 45px;
    cursor: pointer;
    position: relative;
    @extend %box;
    @include flexCenter;
    align-items: center;
    justify-content: center;

    @include respond-to(i7) {
        min-height: 160px;
    }
    @include respond-to(i5) {
        min-height: 170px;
    }

    @include col;
    @include size(3);
    @include size-md(4);
    @include size-sm(6);
    @include size-i5(12);

    &:hover {
        box-shadow: $bxs-hover;
    }

    &.is-checked {

        &:hover {

            .bb-checkbox {

                &__dell {
                    display: block;
                }
            }
        }
    }

    &__wrap {
        padding: $gutter $gutter 0;
        @include row-flex;
    } //.category-item__wrap

    &__icon {
        width: 45px;
        height: 45px;
        @include vx;
        top: 35px;
    } //.lk-service__icon

    &__buttons {
        padding: 17px 20px 20px;
        text-align: center;
        border-top: 1px solid $color-border--lightest;
    }

    .bb-checkbox {

        &__title {
            margin: auto;
        }

        &__box {
            position: absolute;
            top: 10px;
            right: 10px;
        }
    }
} //.category-item

.steps-categoty {

    .bb-checkbox {
        padding: 20px 50px;
        position: relative;

        @include respond-to(xs) {
            padding: 20px 10px 20px 35px;
        }

        &:hover {

            .bb-checkbox {

                &__icon {
                    fill: $color-main;
                }
            }
        }

        & + .bb-checkbox {
            border-top: 1px solid $color-border--lightest;
        }

        &__box {
            @include centre('y');
            left: 20px;

            @include respond-to(xs) {
                left: 10px;
            }
        } //.steps-categoty .bb-checkbox__box

        &__title {
            position: relative;
            top: 1px;
        }

        &__icon {
            width: 22px;
            height: 22px;
            fill: $color-icon-darken;
            @include vy;
            right: 20px;

            @include respond-to(xs) {
                right: 10px;
            }
        } //.steps-categoty .bb-checkbox__icon
    } //.steps-categoty .bb-checkbox
} //.steps-categoty
