.progressbar {

    &__wrap {
        @include row-flex;

        .progressbar {
            @include col();
            @include size(9);
            @include size-md(8);
            @include size-sm(12);
        }
    } //.progressbar__wrap

    .progressbar__title {
        margin-top: 8px;
        font-family: $f-medium;
        font-size: 14px;
        color: #cecece;

        @include respond-to(i5) {
            display: none;
        }
    }

    &__list {
        @include flexCenter;
        justify-content: space-between;
        align-items: center;
    } //.progressbar__list

    &__item {
        width: 100%;
        text-align: center;
        position: relative;

        &::before,
        &::after {
            content: '';
            display: block;
            height: 2px;
            width: 50%;
            background-color: #e8e8e8;
            position: absolute;
            top: 14px;
        }

        &::before {
            left: 15px;
        }

        &::after {
            right: -15px;
        }

        &:first-child {

            &::before {
                display: none;
            }
        }

        &:last-child {

            &::after {
                display: none;
            }
        }

        &.is-checked {

            .progressbar__num {
                background-color: $color-success;

                &::before {
                    content: '\f00c';
                    @include fa('regular');
                    font-size: 17px;
                    color: #fff;
                }
            }

            &::after {
                background-color: $color-success;
            }

            & + .progressbar__item {

                &::before {
                    background-color: $color-success;
                }
            }
        }

        &.is-invalid {

            .progressbar__num {
                color: #fff;
                background-color: $color-danger;
            }
        }

        &.is-current {

            .progressbar__num {
                background-color: #fff;
                border: 3px solid $color-success;
            }

            .progressbar__title {
                color: $gray--darkest;
            }
        }
    } //.progressbar__item

    &__num {
        width: 30px;
        height: 30px;
        margin: auto;
        color: #000;
        font-family: $f-num--m;
        font-size: 15px;
        border-radius: 50%;
        background-color: $gray--lighten;
        position: relative;
        overflow: hidden;
        // @extend %box;
        @include flexCenter;
        justify-content: center;
    } //.progressbar__num
} //.progressbar
