.worker {

    &__inner {
        @extend %box;
        border-radius: $border-radius;

        @include respond-to(xs) {
            margin: 0 -10px;
            box-shadow: none;
        }
    } //.worker__inner

    &__items {

        .phone {
            font-family: $f-num--m;
            font-size: 15px;
            color: $gray;
            position: relative;
            z-index: 1;

            @include respond-to(xs) {
                font-family: $f-num;
                font-size: 14px;
            }
        } //.worker__items .phone

        .icon-status {
            font-size: 20px;

            & + .icon-status {
                margin-left: $gutter / 2;
            }
        } //.worker__items .icon-status
    } //.worker__items

    &__item {
        padding: 15px;
        position: relative;

        @include respond-to(xs) {
            padding-left: 75px;
        }

        &:not(.worker__item--header) {

            &:hover {
                @include respond-min(xs) {
                    background-color: $color-bg--lightest;

                    .worker {

                        &__controls {
                            display: block;
                        }
                    }
                }
            }
        }

        & + .worker__item:not(.worker__item--header) {
            border-top: 1px solid $color-border--lightest;

            &:last-child {
                border-bottom: 1px solid $color-border--lightest;
            }
        }

        &--header {
            font-family: $f-medium;
            font-size: 15px;
            color: $gray--darkest;
            background-color: $color-bg--light;

            @include respond-to(xs) {
                display: none;
            }
        } //.worker__item--header
    } //.worker__item

    &__pagination {
        margin-top: $gutter * 2;
    } //.worker__pagination

    &__empty {
        padding: $gutter;
        margin-bottom: $gutter;
    } //.worker__empty

    &__user {

        .info-block {
            margin: 0;
            padding: 0;
        } //.worker__user .info-block

        .bb-dropdown {

            &:hover {

                .bb-dropdown {

                    &__list {
                        top: 22px;
                    }
                }
            }

            &__list {
                top: 32px;
            }
        } //.worker__user .bb-dropdown
    } //.worker__user

    &__controls {
        margin-left: auto;
        margin-top: -12px;
        position: absolute;
        top: 50%;
        right: 5px;
        display: none;

        @include respond-to(sm) {
            display: block;
            margin-left: 0;

            .control {

                &__item {
                    margin: 0;
                }
            } //.worker__controls .control

            .info-block {
                margin: 0;
                padding: 10px;
                margin-top: -10px;
                position: absolute;
                top: 50%;
                right: -5px;
                z-index: z(fixed) - 1;

                &::before {
                    transform: rotate(0);
                }

                &.is-active {
                    z-index: z(fixed);
                }
            } //.worker__controls .info-block
        }
    } //.worker__controls

    &__link {
        background-color: transparent;
        border: 0;
        outline: none;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 100%;
        width: 88%;

        @include respond-min(sm) {
            display: none;
        }
    } //.worker__link

    .category {
        font-size: 13px;

        &__list {
            margin-right: 70px;
            @include flexCenter;
            flex-wrap: wrap;
        } //.category__list

        &__item {
            margin-bottom: 5px;

            &:not(.category__item--more) {
                cursor: default;
            }

            &--more {
                // flex-basis: 100%;
            } //.category__item--more
        } //.category__item
    }

    .user {
        @include respond-to(xs) {

            &__img {
                @include centre('y');
                left: -52px;
            } //.worker .user__img

            &__position {
                display: none;
            }
        } //.worker .user__position
    } //.worker .user

    .top-line {
        @include respond-to(xs) {
            margin-bottom: $gutter;
        }

        &__search {
            max-width: 425px;
            width: calc(100% - 40px);
            @include flexCenter;
        } //.worker .top-line__search

        &__bnt {

            &_controls {
                width: 40px;
                height: 40px;
            } //.worker .top-line__bnt_controls
        } //.worker .top-line__bnt

        &__reset {
            @include respond-to(xs) {
                // margin-left: $gutter;
                margin: 10px auto 0;
            }
            // @include respond-to(i5) {
            //     margin-left: 10px;
            // }
        } //.worker .top-line__reset
    } //.worker .top-line

    .row {
        position: relative;
        align-items: center;
    } //.worker .row

    .col {
        @include flexCenter;
    } //.worker .col
} //.worker

.worker-add {
    background-color: #fff;
    overflow: hidden;
    position: fixed;
    top: 50px;
    left: 0;
    right: 0;
    bottom: 51px;
    z-index: z(dropdown);
    @include trfX(100vw);
    transition: $trs;
    will-change: transform;

    @include respond-min(xs) {
        display: none;
    }

    &.is-visible {
        @include trfX(0);
    }

    &__inner {
        padding: $gutter;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        @include scroll;
        overflow-x: hidden;
    } //.worker-add__inner

    &__user {
        display: flex;
        justify-content: center;
    } //.worker-add__uset

    &__icon {

        &--close {
            width: 40px;
            height: 40px;
            position: absolute;
            top: 0;
            right: 4px;
            padding: 10px;
            z-index: z(fixed);
        } //.worker-add__icon--close
    } //.worker-add__icon

    .user {

        &__avatar {
            margin-right: 0;
        }
    } //.worker-add .user

    .profile-editor {

        &__item {

            &:last-child {
                margin-bottom: 0;
            }
        }

        &__label {
            width: 100%;
            margin-right: 0;
            margin-bottom: 7px;
        }
    } //.worker-add .profile-editor

    .info-block {
        top: 2px;
        display: none;
    } //.worker-add .info-block

    .add-avatar {
        height: 100%;

        &__inner {
            height: 100%;
            padding: $gutter * 2 $gutter $gutter;
            background-color: #fff;
            position: relative;
            @include scroll;
        }

        &__footer {
            padding: $gutter;
            bottom: 0;
        }
    } //.add-avatar
} //.worker-add
