.services {

    &__title {
        padding-right: 40px;

        &::before {
            margin-right: 0;
            @include vy;
            right: 20px;
        }
    } //.services__title

    &__add-service {
        border-top: 1px solid $color-border--lightest;
    } //.services__add-service

    .bb-accordeon {

        &__title {
            @include respond-to(xs) {
                padding: 18px 10px;
            }

            &::before {
                margin-right: 0;
                @include centre('y');
                right: 19px;
            }

            &:hover {
                box-shadow: none;
            }
        }

        &__item {
            box-shadow: none;
            border-top: 1px solid $color-border--lightest;
            border-style: 0;

            & + .bb-accordeon__item {
                margin-top: 0;
            }

            &:first-child {
                border-top: 0;
            }
        }
    } //.services .bb-accordeon

    .search {
        height: 40px;
        margin: 0 auto $gutter * 2;
        @extend %box;

        &__group {

            &--services {
                border-left: 0;
            }
        }

        &__btn,
        &__input {
            height: 40px;
            line-height: 40px;
        }

        &__btn {
            font-size: 15px;
            border-radius: 0 $border-radius $border-radius 0;
            box-shadow: $bxs-box;
        }
    } //.services .search

    .search-hint {
        border: 0;
        @extend %box;
    } //.services .search-hint
} //.services

.service-item {
    padding: 17px 20px;
    border-top: 1px solid $color-border--lightest;
    position: relative;
    @include nosel;
    @include flexCenter;

    @include respond-to(xs) {
        flex-wrap: wrap;
    }

    &.drag-sort {
        box-shadow: 0 3px 20px rgba(0, 0, 0, 0.2);
        background-color: #fff;
    }

    &:hover {
        background-color: $color-bg--lightest;

        .service-item {

            &__controls {
                display: block;
                opacity: 1;
                visibility: visible;
            }
        }
    }

    &.is-saved {

        .service-item__save {
            display: block;
        }
    }

    &__left {
        flex-grow: 1;
    } //.service-item__left

    &__field {
        margin-left: auto;
        @include flexCenter;

        @include respond-to(xs) {
            width: 100%;
            margin-left: 0;
            margin-top: 15px;
            z-index: 1;
            flex-wrap: wrap;
        }

        .bb-input {

            & + .bb-input {
                margin-left: 15px;
                @include max-screen(370) {
                    margin-left: 5px;
                }
            }

            &__input {
                width: 140px;
                padding-right: 30px;
            }

            &--time {
                width: 140px;
            }

            &--price {
                width: 140px;

                @include respond-to(xs) {
                    width: calc(50% - 10px);
                }

                @include respond-to(i5) {
                    width: 100%;
                    margin-bottom: 15px;
                }

                .bb-input__input {
                    width: 100%;
                    padding: 9px 25px 9px 10px;
                }
            } //.service-item__field .bb-input--price .bb-input__input
        }
    } //.service-item__field

    &__controls {
        margin-left: 20px;
        opacity: 0;
        visibility: hidden;
        position: absolute;
        bottom: 7px;

        @include respond-to(xs) {
            opacity: 1;
            visibility: visible;
            position: absolute;
            left: auto;
            top: 15px;
            right: 10px;
        }
    } //.service-item__controls

    &__select {
        width: 140px;
        margin-left: $gutter;

        @include respond-to(xs) {
            width: calc(50% - 10px);
        }

        @include respond-to(i5) {
            width: 100%;
            margin-left: 0;
        }

        .bb-input,
        .bb-select {
            width: 100%;
        }
    } //.service-item__select

    &__save {
        display: none;
        padding: 0 5px;
        @extend %textLight;
        font-size: 13px;
        color: $color-success;
        border-radius: $border-radius;
        @include vy;
        right: 330px;

        @include respond-to(xs) {
            left: auto;
            right: 70px;
            top: 20px;
            transform: translate(0, 0);
        }
    } //.service-item__save

    &__checkbox {
        display: inline-block;

        @include respond-to(xs) {
            display: block;
            width: 100%;
            margin-right: 50px;
        }
    } //.service-item__checkbox

    &--new {
        background-color: $color-bg--lightest;
    } //.service-item--new

    .control {

        &__text {
            font-family: $f;
            font-size: 13px;
            color: $gray--light;

            @include respond-to(xs) {
                display: none;
            }
        } //.service-item .control__text

        &__icon {
            font-size: 11px;

            @include respond-to(xs) {
                font-size: 15px;
            }
        } //.service-item .control__icon
    } //.service-item .control
} //.service-item

.add-service {
    position: relative;

    &__inner {
        max-width: 600px;
        margin: auto;
        padding: 30px 30px 35px;
        @include flexColumn;

        @include respond-to(xs) {
            padding: 15px 20px 20px;
        }
    } //.add-service__inner

    &__row {
        margin-bottom: $gutter;
        @include flexCenter;
        justify-content: center;

        @include respond-to(xs) {
            flex-wrap: wrap;
        }

        &:last-child {
            margin-bottom: 0;
        }

        &--text {
            margin-bottom: 0;
            text-align: center;
            flex-direction: column;
        } //.add-service__row--text

        p {
            display: block;
            margin-bottom: 20px;
            font-family: $f-light;
            font-size: 15px;
            color: $gray--light;

            @include respond-to(xs) {
                font-size: 14px;
            }

            @include respond-to(i5) {
                font-size: 13px;
            }
        } //.add-service__row p
    } //.add-service__row

    &__btn {
        width: 100%;
        padding: 17px 15px;
        font-size: 16px;
        text-align: center;
        background-color: $color-bg--lightest;
        outline: none;
        justify-content: center;

        &:hover {
            text-decoration: none;

            .icon {
                transform: rotate(-90deg);
            }
        }

        .icon {
            margin-right: 7px;
            fill: $color-main;
            transition: $trs;
        } //.add-service__btn .icon

        &--close {
            position: absolute;
            top: 10px;
            right: 10px;

            @include respond-to(xs) {
                top: 13px;
            }

            @include respond-to(i5) {
                right: 0;

                .icon {
                    width: 15px;
                    height: 15px;
                }
            }
        } //.add-service__btn--close
    } //.add-service__btn

    .bb-input {
        min-width: 120px;
        width: 100%;

        &:first-child {
            min-width: 250px;
            width: 100%;

            @include respond-to(xs) {
                width: 100%;
                margin-left: 0;
            }
        }

        & + .bb-input {
            margin-left: 20px;

            @include respond-to(xs) {
                margin-top: 20px;

                &:not(.bb-input--select) {
                    margin-left: 0;
                }
            }

            @include respond-to(i5) {
                margin-left: 0;
            }
        }

        input {
            width: 100%;
        }

        &--price,
        &--select {
            width: calc(50% - 10px);

            @include respond-to(i5) {
                width: 100%;
            }
        }

        .bb-select {
            @include respond-to(i5) {
                width: 100%;
            }
        }
    } //.add-service .bb-input

    .bb-checkbox-row {

        .bb-checkbox {

            &__title {
                min-width: 120px;
            }
        }
    } //.add-service .bb-checkbox-row
} //.add-service
