.page-cabinet {

    .wrapper {

        &__inner {
            background-color: $color-bg--lighten;
        }
    } //.page-cabinet  .wrapper

    .container {
        @include respond-to(lg) {
            padding: 0;

            &:not(.container--sm) {
                max-width: 100%;
                margin: 0;
                padding: 0;
            }
        }
    } //.page-cabinet .container

    .header {
        // @include border(bottom);
        background-color: rgba(#fff, 0.85);
        box-shadow: 0 0px 3px rgba(0, 0, 0, 0.2);
        position: fixed;
        left: $base-header-size;
        right: 0;
        top: 0;
        z-index: z(dropdown);
        transition: transform ease-out 0.3s, width ease-out 0.3s,
            left 0.3s cubic-bezier(0.25, 0.8, 0.4, 0.95);

        &.is-moving {

            .header__title {
                @include trfX(190px);
            }
        }

        @include respond-to(xs) {
            width: 100%;
            left: 0;
        }

        &__top,
        &__inner {
            height: $base-header-size;
            @include respond-to(xs) {
                height: $base-header-size--small;
            }
        }

        &__title {
            transition: transform ease-out 0.3s;
        }

        &__inner {
            width: 100%;
            border-bottom: 0;
            @include respond-to(xs) {
                display: flex;
                align-items: center;
            }
        }

        &__logo {
            @include min-screen(426) {
                display: none;
            }
        }

        &.bg--dark {

            .header-action__request,
            .header-user {
                border-right: 0;
            }

            .header-action__request-info {
                border: 0;
            }

            .header-user {

                &__img {
                    border: 2px solid $color-border--darken;
                }
            }
        }
    } //.page-cabinet .header

    .header-user {

        &[data-dropdown-position^='right'] {

            .bb-dropdown {

                &__list {
                    right: 10px;

                    &::before,
                    &::after {
                        right: 32px;
                    }
                }
            }
        }
    } //.page-cabinet .header-user

    .header-action {
        @include respond-to(xs) {
            border-left: none;
        }

        &__fav,
        &__request {
            width: $base-header-size;
            height: $base-header-size;
            @extend %xs50;

            &:hover {
                background-color: rgba($color-bg--lighten, 0.45);

                &::before {
                    color: $gray--darkest;
                }
            }
        }

        &__request {
            margin-left: auto;
            border-left: 1px solid $color-border--lightest;
            border-right: 1px solid $color-border--lightest;

            &-info {
                border-left: 1px solid $color-border--lightest;
                border-right: 1px solid $color-border--lightest;
            }
        }
    } //.page-cabinet .header-action

    .request {

        &__items {
            @include respond-to(xs) {
                height: calc(100vh - 127px - 50px);
            }
        }

        .bb-dropdown {

            &__list {
                @include dropdownResponseWidth;

                @include respond-to(xs) {
                    border-top-color: $color-border--lightest;
                    border-left: 0;
                    border-right: 0;
                    border-bottom: 0;
                    right: -50px;
                    width: 425px;

                    &::after,
                    &::before {
                        right: 69px;
                    }
                }
            }
        }
    } //.page-cabinet .request

    .toggle-mnu {
        @include respond-to(sm) {
            display: none;
        }

        @include respond-to(xs) {
            display: block;
        }
    } //.page-cabinet .toggle-mnu

    .btn-fixed {

        &[data-position='bottom right'] {
            right: 30px;
            bottom: 30px;

            @include respond-to(xs) {
                right: 10px;
                bottom: 60px;
            }
        }
    }

    &.menu-open {

        .cabinet {
            width: calc(100% - 225px);
            @include trfX(188px);
        }
    } //.page-cabinet.menu-open

    &.mobile-nav--open {

        .cabinet {
            pointer-events: none;
        }
    } //.page-cabinet.mobile-nav--open

    &.menu-hidden {

        .menu {
            display: none;
        }

        .header {
            left: 0;
        }

        .cabinet {
            margin-left: 0;
        }
    } //.page-cabinet.menu-hidden

    .info-block {

        &[data-dropdown-position^='right'] {
            @include screen(480, 768) {

                .bb-dropdown {

                    &__list {
                        left: 70px;
                    }
                }
            }
        }
    }
} //.page-cabinet

.cabinet {
    width: calc(100% - #{$base-header-size});
    height: calc(100% - #{$base-header-size});
    margin-top: $base-header-size;
    margin-left: $base-header-size;
    padding: $gutter * 1.5 $gutter * 1.5;
    background-color: #fbfbfb;
    transition: transform ease-out 0.3s, width ease-out 0.3s;
    overflow: hidden;

    @include respond-to(md) {
        padding: $gutter 10px;
    }

    @include respond-to(xs) {
        width: 100%;
        height: 100%;
        margin: $base-header-size--small 0;
    }

    &.one-screen {
        padding: 0;
        overflow: hidden;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        @include flexColumn;
        flex: 1 1 auto;
    }

    &.no-padding {
        padding-left: 0;
        padding-right: 0;
        padding-bottom: 0;

        .top-line {
            padding: 0 30px;
        }
    }

    &__title {
        margin-bottom: $gutter;
        font-family: $f-medium;
        font-size: 18px;
        @include flexCenter;

        @include min-screen(426) {
            display: none;
        }

        @include respond-to(xs) {
            font-size: 22px;
        }

        .btn {
            margin-left: auto;
            padding: 7px 15px;
        }
    } //.cabinet__title

    &__logo {
        display: none;
        width: 165px;
        height: 100%;
        margin-right: auto;
        align-items: center;
        justify-content: center;

        @include respond-to(xs) {
            margin-top: 3px;
            @include flex;
            flex: 1;
        }

        &__link {
            color: #fff;
            @include respond-to(xs) {
                color: $gray--darkest;
            }
        }
    } //.cabinet__logo

    &__tab {
        @include respond-to(xs) {
            margin: 0 -10px;
            padding: 0 10px;
        }

        .tab {

            &__titles {
                margin-bottom: $gutter;
            }
        } //.cabinet__tab .tab

        .button-box {
            margin-bottom: $gutter;
            @include flexCenter;

            @include respond-to(xs) {
                margin: 0 10px;
            }
        } //.cabinet__tab .button-box

        .profile-editor {

            &__inner {
                @include respond-to(xs) {
                    padding-top: 0;
                }
            }

            &__form {
                @include respond-to(xs) {
                    background-color: transparent;
                }
            }
        }
    } //.cabinet__tab

    &__toggle {

        .widget {
            margin-bottom: 7px;

            &:first-child {
                @include respond-to(xs) {
                    margin-bottom: 0;
                    border-bottom: 0;
                }
            }

            &__inner,
            &__body {
                @include respond-to(xs) {
                    padding: 0;
                }
            }

            &__body {
                @include respond-to(xs) {
                    min-height: auto;
                }
            }
        }

        .list {

            &__icon {
                @include respond-to(xs) {
                    font-size: 17px;
                }
            } //.cabinet__toggle .list__icon
        } //.cabinet__toggle .list
    } //.cabinet__toggle
} //.cabinet

.c-title {
    margin-bottom: $gutter * 1.5;
    font-family: $f;
    font-weight: 400;
    font-size: 25px;

    @include respond-to(xs) {
        margin-bottom: $gutter;
        font-size: 22px;
    }

    &__wrap {
        margin-bottom: $gutter * 1.5;
        @include flexCenter;

        .c-title {
            margin-bottom: 0;
        }

        .btn {
            margin-left: auto;
        }
    } //.c-title__wrap
} //.c-title

.c-info {

    &__in {
        @include row-flex;

        .widget {
            margin-bottom: $gutter;
            @include col;
            @include size(3);
            @include size-md(4);
            @include size-xs(12);
        }
    } //.c-info__in
} //.c-info

.icon {

    &__list {
        @include flexCenter;
    }

    &__item {
        max-width: 75px;
        margin: auto;
    }
} //.icon

.widget {
    margin-bottom: $gutter * 2;
    @extend %box;
    @include flexColumn;

    @include respond-to(xs) {
        @include resetBox;
    }

    &__wrap {
        @include row-flex;
        align-items: flex-start;

        .widget {
            @include col;
            @include size(3);
            @include size-md(4);
            @include size-sm(6);
            @include size-xs(12);

            &--md {
                @include size(6);
                @include size-md(6);
                @include size-sm(12);
            }
        }

        .card-item {
            @include col;
            @include size(6);
        }
    } //.widget__wrap

    &__header {
        @extend %cardHeader;
        @include flexCenter;

        @include respond-to(xs) {
            padding: 10px;
        }
    } //.widget__header

    &__body {
        min-height: 95px;
        padding: 15px;

        @include respond-to(xs) {
            min-height: auto;
            padding: 10px;
        }
    } //.widget__body

    &__inner {
        padding: 15px;
        @include flex;

        .widget {

            &__left {
                width: 50%;
                padding: $gutter $gutter * 2 $gutter $gutter;
                position: relative;

                @include respond-to(xs) {
                    padding: $gutter $gutter / 2;

                    .list {

                        &__item {
                            margin: 0 0 $gutter / 2;

                            &:last-child {
                                margin-bottom: 0;
                            }
                        }
                    }
                }
            } //.widget__left

            &__right {
                @extend .widget__left;
                padding-right: $gutter;
                padding-left: $gutter * 2;

                &::before {
                    content: '';
                    display: block;
                    height: 80%;
                    width: 1px;
                    background-color: $color-bg--light;
                    @include vy;
                    left: 0;
                }
            } //.widget__right
        }
    } //.widget__body

    &__title {
        color: $gray--darkest;
    } //.widget__title

    &__items {
        min-height: 95px;
        @include flexCenter;
        flex: 1;
    } //.widget__items

    &__item {
        padding: 15px;
        width: 50%;
        height: 100%;
        text-align: center;
        @extend %textLight;
        font-size: 15px;
        color: $color-primary;
        background-color: transparent;
        border: none;
        cursor: pointer;
        position: relative;
        @include tr;
        @include flexColumn;
        align-items: center;

        &::after {
            content: '';
            display: block;
            width: 1px;
            height: 60%;
            background-color: $gray--lighten;
            position: absolute;
            top: 20%;
            left: 0;
        }

        &:first-child {

            &::after {
                display: none;
            }
        }

        &.color-black {
            color: $gray--darken;

            &:hover {
                color: $gray--darken;
            }
        }

        span {
            display: block;
            margin: auto;
            font-family: oswald-500, sans-serif;
            font-size: 28px;
            line-height: 1;
        }

        small {
            display: block;
            margin-top: -3px;
            font-size: 12px;
        }
    } //.widget__item

    &__btn {

        &:hover {
            color: #fff;
            background-color: lighten($color-primary--light, 7%);

            &::after {
                display: none;
            }

            & + .widget__item {

                &::after {
                    display: none;
                }
            }

            .box__btn_top {
                fill: #fff;
            }
        }

        &_top {
            display: block;
            margin: auto;
            width: 28px;
            height: 28px;
            fill: $color-main;
            @include tr;
        } //.box__btn_top
    } //.widget__btn

    &__toggle {
        padding: $gutter;
        @include flexCenter;
        justify-content: center;

        .bb-checkbox {

            &__box {
                top: 0;
            }
        }
    } //.widget__toggle

    &--icon {

        .widget {

            &__body {
                @include flexCenter;

                @include respond-to(xs) {
                    flex-wrap: wrap;
                }
            }
        }

        .icon {

            &__list {
                flex-grow: 1;

                @include respond-to(xs) {
                    margin-bottom: $gutter;
                }
            }
        }

        .btn {
            min-width: 160px;
            margin-left: $gutter;

            @include respond-to(xs) {
                min-width: 100%;
                margin: auto;
            }
        }
    } //.widget--icon

    .info-block {
        margin-left: auto;

        &::before {
            position: relative;
            top: 2px;
        }

        &--right {

            &.is-active,
            &:hover {

                .info-block__dropdown {
                    top: 24px;
                }
            }

            .info-block {

                &__dropdown {
                    top: 33px;
                    right: -5px;
                }
            }
        }
    } //.widget .info-block

    .list {

        &.list-color--success {

            .list {

                &__icon {
                    color: $color-success;
                }
            }
        }
    } //.widget .list
} //.widget

.card-item {
    @extend %box;

    @include respond-to(sm) {
        margin-bottom: 40px;
    }

    @include respond-to(xs) {
        @include resetBox;
    }

    &__wrap {
        @include flex;
        @include respond-to(sm) {
            flex-wrap: wrap;
        }
    } //.card-item__wrap

    &__header {
        @extend %cardHeader;

        @include respond-to(i7) {
            font-size: 15px;
        }

        i {
            margin-right: 10px;
        }
    } //.card-item__header

    &__body {
        @include flexColumn;

        @include respond-to(xs) {
            padding: $gutter / 2;
        }

        p {
            display: block;
            margin-bottom: 15px;
            @extend %textLight;
            font-size: 14px;
        }
    } //.card-item__body

    &__box {
        padding: 20px 15px;
        border-bottom: 1px solid $color-border--lightest;
        @include flexColumn;

        @include respond-to(xs) {
            padding: 0;

            &.mb-17 {
                margin-bottom: 10px;
            }
        }

        .card-item {

            &__subtitle {
                font-size: 14px;
                color: $gray;
            } //.card-item__box .card-item__subtitle
        } //.card-item__box .card-item

        .bb-input {
            margin-right: $gutter;
            flex: 1;

            @include respond-to(md) {

                &:not(.bb-input--phone) {
                    margin-bottom: $gutter / 2;
                    width: 100%;
                }
            }

            @include respond-to(sm) {
                margin-right: 0;
            }

            @include respond-to(i5) {

                &::before {
                    right: 10px;
                }
            }

            &__wrap {
                @include flex;
                align-items: flex-start;

                @include respond-to(sm) {
                    flex-wrap: wrap;
                    justify-content: flex-start;
                }

                & + .bb-input__wrap {
                    margin-top: $gutter;
                }
            } //.card-item__box .bb-input__wrap

            &__title {
                position: static;
                min-width: 160px;
                margin-top: 9px;
                font-family: $f-medium;
                font-size: 16px;
                color: #000;

                @include respond-to(xs) {
                    margin-top: 9px;
                }

                @include respond-to(i5) {
                    min-width: max-content;
                    margin-right: $gutter / 2;
                }
            } //.card-item__box .bb-input__title

            &__input {
                width: 100%;

                @include respond-to(i5) {
                    padding: 9px 20px 9px 10px;
                }
            } //.card-item__box .bb-input__input

            &__message {
                top: 44%;
                right: 0;
                left: 0;

                @include respond-to(md) {
                    top: 100%;
                }
            } //.card-item__box .bb-input__message

            &--phone {
                max-width: 425px;
            } //.card-item__box .bb-input--phone
        } //.card-item__box .bb-input

        &.no-border {
            border-bottom: 0;
        } //.card-item__box--clear

        &--first {
            padding-top: 0;
        } //.card-item__box--first

        &--vk {

            .bb-input {
                max-width: 350px;
                margin-right: auto;

                &__title {
                    min-width: 75px;
                    margin-top: 9px;
                    font-family: $f-medium;
                }
            }
        } //.card-item__box--vk
    } //.card-item__box

    &__title {
        margin-bottom: 10px;
        font-size: 14px;
    } //.card-item__title

    &__caption {
        margin-bottom: 10px;
        font-family: $f;
        font-size: 16px;
        color: #000;
        @include flexCenter;

        span {
            line-height: 1;
        }

        .ico {
            margin-right: 10px;
        }
    } //.card-item__title

    &__social {
        margin-right: 185px;
        @include flexCenter;

        @include respond-to(lg) {
            margin-right: 195px;
        }

        @include respond-to(md) {
            margin-right: 0;
        }

        @include respond-to(xs) {

            .btn-social {
                max-width: 45px;
                min-width: 45px;
                padding: 10px 0;
            }
        }

        .bb-input {
            margin-right: 0;

            &__wrap {
                flex: 1;
            }
        }
    } //.card-item__social

    &__btn {
        min-width: 175px;

        @include respond-to(xs) {
            width: 100%;
        }
    } //.card-item__btn

    .social {
        margin-right: $gutter;

        &__inner {
            max-width: 170px;
            @include flexCenter;

            @include respond-to(i7) {
                max-width: 100%;
            }
        }
    } //.card-item .social

    .btn-social {
        width: max-content;
        text-transform: initial;

        @include respond-to(i7) {
            width: 100%;
        }

        & + .btn-social {
            margin-left: 7px;
        }
    } //.card-item .btn-social

    .button-box {
        margin-bottom: 0;
        @include flexColumn;

        @include respond-to(sm) {
            width: 100%;
        }

        .btn {
            margin: auto;
            max-width: 175px;

            @include respond-to(xs) {
                max-width: 100%;
            }

            & + .btn {
                margin-top: $gutter / 2;
            }
        }
    } //.card-item .button-box

    .bb-accordeon {
        @include respond-to(xs) {
            @include border(top);
        }

        &__inner {
            padding-bottom: $gutter;
            @include border(bottom);
        } //.card-item .bb-accordeon__inner

        &__item {
            border-bottom: 1px solid $color-border--lighten;
            border-radius: 0;

            &:last-child {
                border-bottom: 0;
            }

            &.is-open {
                border-bottom-color: $color-border--lightest;
            }

            & + .bb-accordeon__item {
                margin-top: 0;
            }
        } //.card-item .bb-accordeon__item

        &__title {
            padding: 18px 15px;
            font-size: 15px;
            color: $gray;
            background-color: $color-bg--lightest;

            &::before {
                color: $gray;
                @include fa('light');

                @include respond-to(xs) {
                    right: 11px;
                }
            }

            @include respond-to(xs) {
                padding: 10px;
            }
        } //.card-item .bb-accordeon__title

        &__content {
            padding: 15px 15px 20px;
            border-top: 1px solid $color-border--lightest;

            @include respond-to(xs) {
                padding: 10px;
            }
        } //.card-item .bb-accordeon__content

        .bb-input--title,
        .bb-text--title {
            margin-right: 0;
        } //.card-item .bb-accordeon

        .card-item__box {
            padding: 0 0 20px;

            &:last-child {
                padding-bottom: 0;
                border-bottom: 0;
            }
        } //.card-item .bb-accordeon
    } //.card-item .bb-accordeon
} //card

.box-inline {
    @extend %box;
    @include flexColumn;

    &__header {
        @extend %headerLkStyle;
    } //.box-inline__header

    &__in {
        padding: $gutter;
        @include flex;

        @include respond-to(xs) {
            padding: $gutter $gutter / 2;
        }
    } //.box-inline__body

    &__text {
        line-height: 1.2;

        &--right {
            margin-left: auto;
        } //.box-inline__text--right

        i {
            font-size: 11px;
        }
    } //.box-inline__text

    &__title {
        margin-right: 15px;
        font-family: $f-medium;
        font-size: 15px;
        line-height: 1.2;
        color: $gray--darkest;
    } //.box-inline__title

    &__subtitle {
        padding-left: 15px;
        color: $gray--light;
        font-size: 14px;
        white-space: nowrap;
        position: relative;

        &::before {
            content: '';
            display: block;
            height: 100%;
            width: 1px;
            background-color: $gray--light;
            @include vy;
            left: 0;
        }
    } //.box-inline__subtitle

    &__controls {
        margin-left: auto;
    } //.box-inline__control

    &__list {
        padding-left: 10px;
        border-left: 3px solid $color-border--light;
        @include flexColumn;

        &.is-success {
            border-color: $color-success;
        }

        &.is-abort {
            border-color: $color-danger;
        }

        & + & {
            margin-top: $gutter;
        }
    } //.box-inline__list

    &__item {
        @include flexColumn;

        & + & {
            margin-top: 5px;
        }
    } //.box-inline__item

    &__line {
        font-size: 15px;
        line-height: 1.3;
        @include flexCenter;

        &--bottom {
            font-family: $f-light-i;
            font-size: 14px;
            color: $gray--light;
        }

        .box-inline__text {
            line-height: 1.5;
        }
    } //.box-inline__line
} //.box-inline

.box-with-img {
    @include row-flex;
    align-items: center;

    &__left {
        @include col;
        @include size(8);
        @include size-xs(12);
    } //.box-with-img__left

    &__right {
        @include col;
        @include size(4);
        @include size-xs(12);
    } //.box-with-img__right

    &__img {
        max-width: 170px;
        margin: 0 auto;

        @include respond-to(xs) {
            margin: $gutter auto $gutter * 2;
        }
    } //.box-with-img__img
} //.box-with-img

.top-line {
    margin-bottom: 30px;
    position: relative;
    @include flex;
    align-items: flex-start;

    @include respond-to(md) {
        margin-bottom: 20px;
    }

    &__sorting {
        @include flexCenter;
        flex-grow: 1;

        @include respond-to(md) {
            flex-wrap: wrap;
            align-items: baseline;
        }

        @include respond-to(xs) {
            width: 100%;
            flex-direction: column;
        }
    } //.top-line__sorting

    &__search {
        margin-right: $gutter * 2;

        @include respond-to(md) {
            width: 50%;
            margin-bottom: $gutter;
            margin-right: $gutter;
        }

        @include respond-to(xs) {
            width: calc(100% - 40px);
            margin-bottom: $gutter / 2;
            margin-right: 40px;
        }

        @include respond-to(i5) {
            margin-right: 0;
        }

        .search {
            max-width: 100%;
            height: 40px;

            @include respond-to(xs) {
                width: 100%;
            }

            @include respond-to(i5) {
                margin: 0;
            }

            &__input {
                height: 40px;
                background-color: transparent;
                border-radius: $border-radius;
                border: 1px solid $color-border--light;
                outline: none;

                &:hover {
                    border-color: $color-border--light;
                }

                &:focus {
                    border-color: $color-border--lighten;
                    box-shadow: $bxs-box;
                }

                @include respond-to(xs) {
                    width: 100%;
                }
            }
        }

        .bb-input {
            width: 100%;
        }
    } //.top-line__search

    &__date {
        margin-right: $gutter;

        @include respond-to(md) {
            margin-bottom: $gutter;
        }
    } //.top-line__search

    &__select {

        & + & {
            margin-left: $gutter;
        }

        &.w300 {
            min-width: 300px;
        }

        @include respond-to(md) {
            margin-bottom: $gutter;
        }

        @include respond-to(xs) {
            // width: calc(50% - 5px);
            width: 100%;
            margin-bottom: $gutter / 2;
            margin-right: 0;

            & + & {
                margin-left: 0;
            }

            &:last-child {
                margin-bottom: 0;
            }

            .bb-select {
                width: 100%;
            }
        }

        @include respond-to(i5) {
            width: 100%;

            & + & {
                margin-left: 0;
            }
        }

        .bb-input--select {
            min-width: 200px;
            width: 100%;

            @include respond-to(xs) {
                min-width: 100%;
                background-color: #fff;
            }
        }
    } //.top-line__select

    &__btn {
        margin-left: auto;
        flex: none;

        @include respond-to(md) {
            padding: 7px 15px;
        }

        &_controls {
            display: none;
            position: absolute;
            top: 4px;
            right: 0;

            @include respond-to(xs) {
                display: flex;
            }

            @include respond-to(i5) {
                top: -53px;
            }
        }
    } //.top-line__btn
} //.top-line

.control-box {
    @include flexCenter;

    @include respond-to(xs) {
        display: none;
        width: 100%;
        flex-wrap: wrap;
    }
} //.controls-box

.adreses {

    &__list {
        @include flexColumn;
        align-items: flex-start;
    } //.adreses__list

    &__item {
        display: block;
        padding: 5px 30px 5px 0;
        position: relative;

        &:hover {

            .adreses__icon {
                display: block;
            }
        }
    } //.adreses__item

    &__link {
        font-family: $f-light;
        font-size: 14px;
        color: $color-text;
        transition: initial;

        &:hover {
            color: #000;
        }
    } //.adreses__link

    &__icon {
        display: none;
        width: 9px;
        height: 9px;
        fill: $gray--light;
        position: absolute;
        top: 0px;
        right: 15px;

        &:hover {
            fill: #000;
        }
    } //.adreses__icon
} //.adreses

.empty {
    @include flexColumn;
    align-items: center;
    justify-content: center;

    &__block {
        margin-bottom: $gutter;
        @include flexCenter;

        .empty__text {
            margin-right: $gutter;
        }
    } //.empty__block

    &__title {
        display: block;
        margin-bottom: 10px;
        color: $gray--darken;
        font-family: $f-light;
        font-size: 26px;
        text-align: center;

        @include respond-to(xs) {
            font-size: 21px;
        }
    } //.empty__title

    &__text {
        font-family: $f;
        font-size: 15px;
    } //.empty__text

    &__icon {
        width: 70px;
        height: 70px;
        fill: $color-icon;
        color: $color-icon;
        font-size: 100px;
    } //.empty__icon

    &--inline {
        flex-direction: row;

        .empty__icon {
            margin-right: $gutter;
        }
    } //.empty--inline

    .button-box {
        @include flexCenter;

        .btn {

            & + .btn {
                margin-left: $gutter;
            }

            &-link {
                padding: 13px 0;
            }
        }
    }
} //.empty
