.profile-editor {

    &__in {
        @include row-flex;
        align-items: flex-start;
    } //.profile-editor__in

    &__progressbar {
        margin-top: $gutter;
        margin-bottom: $gutter * 2;

        @include respond-to(xs) {
            margin: $gutter * 1.5 0;
        }
    } //.profile-editor__progressbar

    &__steps {
        @include col();
        @include size(2);
        @include size-lg(3);
        @include size-xs(12);

        @include respond-to(xs) {
            margin-bottom: $gutter;
        }
    } //.profile-editor__steps

    &__form {
        width: 100%;
        position: relative;
        @extend %box;

        @include respond-to(xs) {
            margin: 0 -10px;
            width: calc(100% + 20px);
            border-bottom: 1px solid $color-border--lightest;
            border-radius: 0;
            box-shadow: none;
        }

        &--small {

            .profile-editor {

                &__inner {
                    max-width: 768px;
                    margin: auto;
                }
            }
        } //.profile-editor__form--small

        .btn-link--pos {
            font-family: $f-light;
            position: absolute;
            top: -37px;
            right: 0;
        }
    } //.profile-editor__form

    &__info {
        padding: 15px 20px;
        @extend %box;
        @include col();
        @include size(3);
        @include size-md(4);
        @include size-sm(12);

        @include respond-to(sm) {
            margin-bottom: $gutter;
            padding: 10px;
            order: 1;
        }
    } //.profile-editor__info

    &__header {
        @extend %headerLkStyle;
        position: relative;

        &_icon {
            width: 21px;
            height: 21px;
            fill: $color-icon;
        }

        .btn-link {
            padding: 0;
            line-height: 1;
            outline: none;
            @include centre('y');
            right: 14px;
        }
    } //.profile-editor__header

    &__inner {
        padding: $gutter;

        @include respond-to(xs) {
            padding: $gutter $gutter / 2;
        }

        &--column {
            @include flexColumn;
            justify-content: center;
            align-items: center;
        }

        .devider {
            margin: 20px 0;
        }
    } //.profile-editor__inner

    &__footer {
        padding: 20px 0;
        text-align: center;
        border-top: 1px solid $color-border--lightest;

        .btn {
            font-family: $f-medium;
            font-size: 16px;
            letter-spacing: 0.5px;
        }
    } //.profile-editor__footer

    &__item {
        @include itemGutter(24px);
        display: flex;

        @include respond-to(sm) {
            margin-bottom: $gutter;
            flex-wrap: wrap;
        }

        &.mb-30 {
            margin-bottom: 35px;
        }

        &_info {
            justify-content: center;
            flex-direction: column;
        }

        &-inner {
            position: relative;
            display: flex;

            @include respond-to(sm) {
                flex-wrap: wrap;
            }
        }

        &--column {
            flex-direction: column;

            .profile-editor {

                &__item-inner {

                    .profile-editor__label {
                        width: 195px;
                        margin-bottom: 0;
                        font-size: 13px;
                        text-align: right;

                        @include respond-to(sm) {
                            margin-bottom: 5px;
                            font-size: 15px;
                            text-align: left;
                        }
                    }
                }

                &__label {
                    width: 100%;
                    margin-bottom: 5px;
                    text-align: left;
                }
            }
        }

        .color-success,
        .color-danger {
            margin-left: 15px;
            font-family: $f-light;
            font-size: 14px;
            @include respond-to(xs) {
                margin-left: auto;
            }
        }

        .bb-select {
            width: 100%;
        }
    } //.profile-editor__item

    &__label {
        width: 195px;
        margin-right: 10px;
        text-align: right;
        font-size: 13px;

        @include respond-to(md) {
            width: 170px;
        }

        @include respond-to(sm) {
            width: 100%;
            margin-right: 0;
            margin-bottom: 5px;
            padding-top: 0;
            text-align: left;
        }
    } //.profile-editor__label

    &__input {
        width: 320px;
        position: relative;
        @include flexCenter;
        flex-wrap: wrap;

        @include respond-to(sm) {
            width: 100%;
        }

        input {
            width: 100%;
            @extend %styleInput;
            background-color: transparent;
        } //.profile-editor__imput input

        &.is-invalid,
        &.is-error {

            input,
            textarea,
            .select2-selection {
                border: 1px solid $color-danger;
            }

            .profile-editor__error {
                display: block;
            }

            .bb-input-tip {
                display: block;
                color: $color-danger;
                background: #fbebee;
                border-color: rgba(247, 73, 105, 0.75);

                &__arrow-up {

                    &::before {
                        background: #fbebee;
                        border-color: rgba(247, 73, 105, 0.75);
                    }
                }
            }

            textarea {
                margin-bottom: 1px;
            }
        } //.profile-editor__imput.is-invalid

        &.is-info {

            input,
            textarea,
            .select2-selection {
                border: 1px solid $color-info;
            }

            .bb-input-tip {
                display: block;
                color: $color-info;
                background: #ebf5fd;
                border-color: rgba(40, 131, 210, 0.75);

                &__arrow-up {

                    &::before {
                        background: #ebf5fd;
                        border-color: rgba(40, 131, 210, 0.75);
                    }
                }
            }

            textarea {
                margin-bottom: 1px;
            }
        }

        &_confirm {
            margin-bottom: 5px;
            position: relative;

            &::before {
                @include fa('solid');
                @include vy;
                right: 5px;
            }

            input {
                padding-right: 30px;
                @include respond-to(sm) {
                    max-width: 100%;
                }
            }

            &.is-invalid {
                margin-bottom: 50px;

                .profile-editor__attention {
                    top: 60px;
                }
            }

            &.is-confirm {
                margin-bottom: 20px;

                &::before {
                    content: '\f00c';
                    @include fa('regular');
                    color: $color-success;
                    font-size: 16px;
                }

                .profile-editor__attention {

                    &--invalid {
                        display: none;
                    }

                    &--success {
                        display: block;
                    }
                }
            }

            &.is-info {
                margin-bottom: 55px;
            }

            &.is-error {

                &::before {
                    content: '\f12a';
                    color: $color-danger;
                    font-size: 16px;
                    right: 10px;
                }

                .profile-editor__attention--success {
                    display: none;
                }
            }

            .color-success {
                font-family: $f-light;
                font-size: 14px;
            }
        } //.profile-editor__imput_confirm

        &--born {

            .profile-editor__select + .profile-editor__select {
                margin-left: 20px;
            }

            .btn {
                margin-left: 20px;
            } //.profile-editor__imput--born .btn
        } //.profile-editor__imput--born

        .bb-date {
            width: 150px;

            &__input {
                padding-right: 30px;
            }
        } //.profile-editor__imput .bb-date

        .bb-checkbox {
            margin-right: 30px;
            @include flexCenter;

            @include respond-to(xs) {
                margin-right: 10px;
            }

            &:last-child {
                margin-right: 0;
                margin-bottom: 0;
            }

            &__title {
                font-size: 14px;
            }

            &__box {
                display: inline-block;
                top: -1px;
            } //.profile-editor__input .bb-checkbox__box
        } //.profile-editor__imput .bb-checkbox

        .bb-input {
            width: 100%;
        } //.profile-editor__imput .bb-input

        .btn-link-info {
            @include centre('x');
            bottom: -20px;
        } //.profile-editor__imput .btn-link-info
    } //.profile-editor__imput

    &__title {
        display: block;
        margin-bottom: $gutter;
        font-family: $f-medium;
        font-size: 15px;
        color: $gray--darkest;

        &--small {
            margin-bottom: $gutter / 2;
        }
    } //.profile-editor__title

    &__confirm {
        display: block;
        max-width: 300px;
        margin: 5px 0 $gutter/2 205px;

        @include respond-to(sm) {
            margin-left: 0;
        }

        &.ml-0 {
            margin-left: 0;
        }

        .confirm {

            &__link {
                margin-bottom: 0;
            } //.profile-editor__confirm .confirm__link
        }
    } //.profile-editor__confirm

    &__attention {
        display: block;
        max-width: 300px;
        @extend %textLight;
        font-size: 12px;
        line-height: 1.2;
        color: $gray--light;
        position: absolute;
        top: 45px;
        left: 0;

        @include respond-to(sm) {
            margin-left: 0;
        }

        &.ml-0 {
            margin-left: 0;
        }

        &--success {
            display: none;
        }
    } //.profile-editor__attention

    &__select {

        & + & {
            margin-left: auto;

            @include respond-to(xs) {
                margin-left: 15px;
            }

            @include respond-to(i5) {
                margin-left: 2px;
            }
        } //.profile-editor__select+.profile-editor__select

        &--materials {
            width: 100%;
        } //.profile-editor__select--materials

        &--month {

            select,
            .select2-container {
                min-width: 110px;
                width: 110px !important;
            }
        }

        &--day,
        &--year,
        &--color {

            select,
            .select2-container {
                min-width: 80px;
                width: 80px !important;
            }
        }

        .select,
        .select2-container--default .select2-selection--single {
            height: 40px;
            line-height: 40px;
            font-size: 14px;
        }

        .select2-container--default
        .select2-selection--single
        .select2-selection__rendered {
            line-height: 39px;

            @include respond-to(xs) {
                line-height: 41px;
            }
        }

        .select2-container--default
        .select2-selection--single
        .select2-selection__arrow
        b {
            top: 1px;
        }

        &.is-invalid {

            .select2-container--default .select2-selection--multiple,
            .select2-container--default .select2-selection--single {
                border-color: $color-danger;
            }

            .select2-container--default.select2-container--open.select2-container--below
            .select2-selection--single:hover {
                border-color: $color-danger;
            }

            .profile-editor__error {
                display: block;
            }
        } //.profile-editor__select.is-invalid
    } //.profile-editor__select

    &__error {
        display: none;
        font-size: 13px;
        color: $color-danger;
        @include text-overflow;
        position: absolute;
        bottom: -20px;
        left: 0;
    } //.profile-editor__error

    &__checkbox {
        margin-bottom: 10px;

        &:last-child {
            margin-bottom: 0;
        }
    } //.profile-editor__imput

    &__add {
        margin-left: 15px;
        position: relative;
        top: -2px;
    } //.profile-editor__add

    &__map {
        padding-bottom: 33.33%;
        position: relative;
        overflow: hidden;

        @include respond-to(xs) {
            padding-bottom: 56.25%;
        }

        #cabinet-map {
            @include coverdiv;
        }
    } //.profile-editor__maps

    &__upload {
        padding: $gutter $gutter 0;

        .bb-upload {

            &__item {
                @include size(3);
                @include size-lg(4);
            }
        }
    } //.profile-editor__upload

    &--steps {
        max-width: 768px;
        margin: auto;

        .profile-editor {

            &__attention {
                margin-left: 0;
            }

            &__confirm {
                margin-left: 0;
            }
        }
        @include respond-to(xs) {

            .bb-accordeon {
                @include respond-to(xs) {
                    margin: -20px;
                }
            }

            .bb-accordeon__item + .bb-accordeon__item {
                margin-top: 0;
                border-top: 1px solid #eee;
            }
        }
    } //.profile-editor--steps

    .devider {
        margin: $gutter / 2 0 $gutter;
    }

    .selectize-control {

        .selectize-input {
            font-size: 13px;

            input {
                height: 14px;
            }
        }
    }
} //.profile-editor

.profile-editor-info {

    &__title {
        display: block;
        margin-bottom: 5px;
        font-family: $f-bold-i;
    } //.profile-editor-info__title

    &__desc {
        color: $gray--light;
        font-family: $f-i;
        font-size: 15px;

        & + & {
            margin-top: $gutter / 2;
        }

        .text-color--black {
            color: $gray--dark;
            font-family: $f-medium-i;
        }
    } //.profile-editor-info__desc
} //.profile-editor-info
