.bb-upload {
    position: relative;

    &__home {
        padding: 3px 0;
        font-family: $f-light;
        color: #fff;
        text-align: center;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        background-color: rgba(0, 0, 0, 0.4);
        @include tr;
    } //.bb-upload__home

    &__button {
        width: 150px;
        height: 100px;
        margin-bottom: 0;
        border: 1px solid #000;
        border-radius: 5px;
        position: relative;
        cursor: pointer;

        &::before {
            content: '';
            display: block;
            width: 25%;
            height: 2px;
            background-color: #000;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translateX(-50%);
        }

        &::after {
            content: '';
            display: block;
            width: 25%;
            height: 2px;
            background-color: #000;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translateX(-50%) rotate(90deg);
        }

        .fa {
            font-size: 28px;
            cursor: pointer;
        }

        input {
            display: none;
        }
    } //.bb-upload__button

    &__year {
        margin-top: 15px;
        font-family: $f-bold;
        font-size: 14px;
        color: $gray--light;
    } //.bb-upload__time

    &__list {
        margin-top: 5px;
        padding: 0;
        list-style-type: none;
        @include row-flex();

        .bb-upload__item {
            @include col();
            @include size(4);
            @include size-sm(6);
            @include size-xs(12);
        }
    } //.bb-upload__list

    &__item {
        display: inline-block;
        height: auto;
        max-height: 185px;
        margin-bottom: $gutter;
        position: relative;
        cursor: pointer;
        overflow: hidden;
        @extend %box;

        @include respond-to(i7) {

            .fa {

                &.fa-undo {
                    bottom: 0;
                    left: 0;
                }

                &.fa-trash-o {
                    bottom: 0;
                    right: 0;
                }
            }
        }

        &:hover {

            .bb-upload {

                &__overlay {
                    visibility: visible;
                    opacity: 1;
                    filter: none;
                    pointer-events: auto;
                }
            }
        }

        &:first-child {

            .bb-upload__btn--first {
                display: none;
            }
        }
    } //.bb-upload__item

    &__img {
        display: block;
        max-width: 100%;
        height: 100%;
        object-fit: cover;
    } //.bb-upload__img

    &__count {
        display: block;
        width: 17px;
        height: 17px;
        text-align: center;
        font-size: 11px;
        font-weight: bold;
        color: #fff;
        border-radius: 50%;
        background-color: #000;
        position: absolute;
        top: -8px;
        left: -8px;
        z-index: 99;
    } //.bb-upload__count

    &__overlay {
        border-radius: $border-radius;
        overflow: hidden;
        opacity: 0;
        visibility: hidden;
        position: absolute;
        top: 10px;
        right: 10px;
        left: 10px;
        @include flexCenter;
        justify-content: center;
        @include respond-to(sm) {
            opacity: 1;
            visibility: visible;
        }

        i {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 16px;
            color: #fff;
            opacity: 0.7;
            width: 28px;
            height: 28px;
            border: none;
            background-color: #000;
            cursor: pointer;
            transition: all 0.35s ease;

            &:hover {
                opacity: 1;
            }
        }
    } //.bb-upload__overlay

    &__loader {
        background-color: rgba(#fff, 0.9);

        .loader--scroll {
            @include vcenter;
            @include respond-to(xs) {
                top: 20%;
            }
        }
    } //.bb-upload__loader

    &__btn {
        font-size: 14px;
        color: #fff;
        opacity: 0.7;
        width: 28px;
        height: 28px;
        border: none;
        background-color: #000;
        cursor: pointer;
        transition: all 0.35s ease;
        @include flexCenter;
        justify-content: center;

        &:hover {
            opacity: 1;
            color: #fff;
        }

        &:nth-child(2) {
            margin-left: auto;
            border-radius: $border-radius 0 0 $border-radius;
        }

        &--wide {
            width: auto;
            padding: 0 8px;
            font-size: 13px;
            white-space: nowrap;
        } //.bb-upload__btn--wide

        &--first {
            margin-right: 10px;
            border-radius: $border-radius;

            @include respond-to(xs) {
                margin-right: auto;
            }
        } //.bb-upload__btn--first
    } //.bb-upload__btn

    &__empty {
        margin-bottom: $gutter;
    } //.bb-upload__empty
} //.bb-upload
