.search {
    @include flex;
    max-width: 585px;
    width: 100%;
    height: 45px;
    position: relative;
    @include clr;

    @include respond-to(sm) {
        max-width: 100%;
        margin-bottom: 0;
    }

    @include respond-to(i5) {
        margin: 0 auto 50px;
    }

    &__group {
        position: relative;
        display: flex;

        &--services {
            border-left: 1px solid $color-border--lightest;
            flex: 1;
            @include respond-to(xs) {
                border-left: none;

                .search__input {
                    border-radius: $border-radius 0 0 $border-radius;
                }
            }

            .search__input {
                padding-right: 15px;
            }
        }

        &--city {
            flex: 1;

            &::before {
                content: '\f3c5';
                @include fa('light');
                font-size: 16px;
                color: $gray--lighten;
                @include vy;
                right: 8px;
            }

            .search__input {
                border-radius: $border-radius 0 0 $border-radius;
            }
        } //.search__group--city
    } //.search__group

    &__hint {
        position: absolute;
        top: 110%;
        left: -5px;
        right: -5px;
        z-index: z(dropdown);

        .search-hint {
            border-radius: $border-radius;
        }
    } //.search__hint

    &__input {
        width: 100%;
        height: 45px;
        color: $gray--dark;
        padding: 11px 30px 11px 10px;
        border: none;
        border-radius: 0;
        box-sizing: border-box;

        @include placeholder {
            font-family: $f-light-i;
            font-size: 14px;
        }

        &:hover,
        &:focus {
            border: 0;
            box-shadow: none;
        }
    } //.search__input

    &__btn {
        height: 45px;
        line-height: 45px;
        padding: 0 35px;
        text-transform: initial;
        font-size: 17px;
        border-radius: 0 $border-radius $border-radius 0;
        outline: none;
        box-shadow: none;

        &:hover {
            box-shadow: none;
        }
        @include respond-to(xs) {
            display: flex;
            padding: 0 20px;

            span {
                display: none;
            }
        }
        @include respond-to(i5) {
            padding: 0 15px;
        }
    } //.search__btn

    &__ico {
        font-size: 16px;
        color: $color-main;
        @include centre('y');
        right: 10px;
    } //.search__ico

    &__icon {
        width: 25px;
        height: 25px;
        fill: #fff;
        display: none;
        @include respond-to(xs) {
            display: flex;
            margin: auto;
        }
    } //.search__icon

    &--sm {
        max-width: 320px;
        width: 100%;

        .search {

            &__input {
                border-radius: 20px;
            }
        }
    } //.search--sm

    &--dark {

        .search {

            &__input {
                color: $gray--light;
                background-color: $gray--darken;

                &:focus {
                    color: $gray--dark;
                    background-color: #fff;
                }
            }
        }
    } //.search--dark

    .bb-input {
        width: 100%;
    } //.search .bb-input
} //.search

.search-hint {
    max-height: 250px;
    border-radius: $border-radius * 2;
    box-shadow: $bxs-box;
    background-color: #fff;
    overflow-y: auto;
    @include bb-scroll;

    &__item {

        &.is-selected {
            background-color: #eee;
        }
    } //.search-hint__item

    &__link {
        padding: 7px 10px;
        @include flexCenter;
        transition: initial;
        display: flex;

        &:hover {
            background-color: $color-bg--light;
        }
    } //.search-hint__link

    &__bade {
        width: 65px;
        margin-right: 5px;
        padding: 1px 5px;
        @extend %textLight;
        text-align: center;
        text-transform: uppercase;
        font-size: 10px;
        color: $color-text;
        border-radius: $border-radius;

        &--service {
            background-color: $color-success--light;
            color: #fff;
            @include respond-to(xs) {
                width: 65px;
            }
        }

        &--master {
            background-color: $color-primary--light;
            color: #fff;
        }

        &--salon {
            background-color: $color-muted;
            color: #fff;
        }

        &--category {
            background-color: $color-info--light;
            color: #fff;
        }

        &--location {
            width: auto;
            margin-right: 0;

            i {
                font-size: 14px;
            }
        }
    } //.search-hint__bade

    &__text {
        @extend %textLight;
        font-size: 14px;
        color: $color-text;
        flex: 1;
    } //.search-hint__text

    &--cyties {

        .search-hint {

            &__item {
                border-bottom: 1px solid $color-bg--light;

                &:last-child {
                    border-bottom: none;
                }
            }
        }
    } //.search-hint--cyties
} //.search-hint

.mobile-search {
    display: none;
    background-color: #fff;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    &__header {
        padding: 10px 15px 10px 10px;
        background-color: $color-bg--lighten;
        border-bottom: 1px solid $color-border--lightest;
        @include flexCenter;
    } //.mobile-search__header

    &__input {
        height: 35px;
        margin-right: 15px;
        position: relative;
        flex: 1;

        input {
            height: 35px;
            line-height: 35px;
        }
    } //.mobile-search__input

    &__close {

        &:hover,
        &:focus {
            text-decoration: none;
        }
    } //.mobile-search__closse

    .search {
        height: 100%;

        &__group {
            width: 100%;
            position: relative;
            flex-direction: column;
        }

        &__hint {
            position: absolute;
            top: 56px;
            left: 0;
            bottom: 0;
            right: 0;
        }

        &__input {
            padding-right: 30px;
            border: 1px solid $color-border--light;
            border-radius: $border-radius;
        }
    }

    .search-hint {
        max-height: 100%;
        top: 0;
        border: none;
        box-shadow: none;
    }

    .bb-input {

        &__icon {
            color: $color-main;
        } //.search-mobile .bb-input__icon
    } //.search-mobile .bb-input
} //.search-mobile

.popular-search {
    max-width: 565px;
    margin: 0 auto 50px 0;
    padding: 1px 10px 3px;
    background: rgba($color-bg--dark, 0.5);
    border-radius: $border-radius;

    @include respond-to(xs) {
        display: none;
    }

    &__item {
        display: inline-block;
        margin-right: 10px;
        font-family: $f-bold;
        font-size: 12px;
        color: #fff;
        text-transform: uppercase;
    } //.popular-search__item

    &__link {
        font-family: $f-light;
        letter-spacing: 1px;
        color: #fff;

        &:hover {
            text-decoration: underline;
            color: #fff;
        }
    } //.popular-search__link
} //.popular-search

.city-select {
    @include flexCenter;

    &__title {
        margin-right: 20px;
        font-size: 13px;
        border-bottom: 1px dashed $color-border;
        cursor: pointer;
        position: relative;

        &::after {
            content: '';
        }

        &:hover {
            color: $gray--dark;
        }
    } //.city-select__title

    &__icon {
        width: 15px;
        height: 15px;
        font-size: 13px;
        fill: $gray;
        color: $gray;
        @include centre('y');
        right: -20px;
    } //.city-select__icon

    &__list {
        max-height: 250px;
        padding: 0;
        border: 0;
    } //.city-select__list

    &__search {
        padding: 10px;
        background-color: $color-bg--lighten;
        border-bottom: 1px solid $color-border--lightest;
        border-radius: $border-radius $border-radius 0 0;

        input {
            padding: 5px 10px;
        }
    } //.city-select__search

    &__item {
        padding: 5px 10px;
        font-size: 14px;
        cursor: pointer;

        &:hover {
            color: #000;
            background-color: $color-bg--lighten;
        }
    } //.city-select__item
} //.city-select
