$widget-colors: (
    new: $color-info,
    work: $color-warning,
    done: $color-success,
    abort: $color-danger
);

.request {

    &__widget {
        @each $theme, $color in $widget-colors {

            &--#{$theme} {

                .widget {

                    &__header {
                        color: #fff;
                        background-color: $color;
                    }
                }

                .request-item {

                    &__date {
                        @include respond-to(xs) {
                            border-left: 4px solid $color;
                        }
                    }

                    &__icon {
                        color: $color;
                    }
                }
            }
        }

        .widget {

            &__header {
                text-align: center;
                border-bottom: 0;
                justify-content: center;

                @include respond-to(md) {
                    display: none;
                }
            }
        }
    } //.request__widget

    &__tab {
        @include respond-min(xs) {
            @include row-flex;
            align-items: flex-start;
        }

        @include respond-to(xs) {
            margin-left: -10px;
            margin-right: -10px;
        }

        .request__widget {
            @include col;
            @include size(3);
            @include size-md(12);

            @include respond-to(xs) {
                width: 100%;
                margin-left: 0;
                margin-right: 0;
                box-shadow: none;
                background-color: transparent;
            }
        }
    } //.request__tab

    &__empty {
        @include flexCenter;
        flex-direction: row;

        .empty {

            &__icon {
                margin-right: $gutter;
            }
        }
    } //.request__empty

    .tab {

        &__titles {
            margin-bottom: $gutter;

            @include respond-min(md) {
                display: none;
            }

            @include respond-min(xs) {
                @include col;
                @include size(12);
            }

            @include respond-to(xs) {
                margin: 0 10px 20px;
            }
        }

        &__link {
            font-family: $f-medium;
            font-size: 15px;
        }

        &__title {
            @each $theme, $color in $widget-colors {

                &--#{$theme} {

                    .tab {

                        &__icon {
                            color: $color;
                        }
                    }

                    &.ui-tabs-active,
                    &:hover {

                        .tab {

                            &__link {
                                border-bottom-color: $color;
                            }

                            &__icon {
                                color: $color;
                            }
                        }
                    }
                }
            }
        }
    } //.request .tab

    .bb-date {
        width: 100%;
    }

    .top-line {

        &__date,
        &__search,
        &__select {
            @include respond-to(sm) {
                width: 38%;
            }
        }

        &__date {
            max-width: 140px;
            position: relative;
            @include flexCenter;

            @include respond-to(xs) {
                max-width: 100%;
                width: calc(50% - 8px);
                margin-right: 16px;
            }

            @include respond-to(i5) {
                width: 100%;
                margin-right: 0;
            }

            &:nth-child(2) {
                @include respond-to(xs) {
                    margin-right: 0;
                }

                &::before {
                    content: '—';
                    @include vy;
                    left: -16.5px;

                    @include respond-to(xs) {
                        left: -14.5px;
                    }

                    @include respond-to(i5) {
                        display: none;
                    }
                }
            }

            .bb-date {
                @include respond-to(xs) {

                    &__input {
                        padding-right: 18px;
                        padding-left: 5px;
                    } //.request .top-line__date .bb-date__input

                    i {
                        padding: 5px;
                        right: 0;
                    } //.request .top-line__date .bb-date i
                }
            } //.request .top-line__date .bb-date
        } //.request .top-line__date
    } //.request .top-line
} //.request

.request-item {
    display: block;
    padding: $gutter / 2;
    border: 1px solid $color-border--lightest;
    border-radius: $border-radius * 2;
    background-color: #fff;
    transition: initial;
    overflow: hidden;
    position: relative;

    @include respond-to(xs) {
        padding: 0;
        border-left: 0;
        border-right: 0;
        border-radius: 0;

        &:not(:first-child) {
            border-top: 0;
        }
    }

    & + & {
        margin-top: $gutter / 2;

        @include respond-to(xs) {
            margin-top: 0;
        }
    }

    &:first-child {
        margin-top: 0;
    }

    &__inner {
        @include respond-to(xs) {
            margin-left: 60px;
            padding: 10px;
            // border: 1px solid $color-border--lightest;
            border-left: transparent;
            display: flex;
            flex-direction: column;
        }
    } //.request-item__inner

    &__link {
        color: $color-text;
        // @include coverdiv;

        @include respond-to(sm) {
            // display: none;
        }
    } //.request-item__link

    &__title {
        margin-right: 15px;
        margin-bottom: 0;
        font-family: $f-medium;
        font-size: 15px;
        color: $gray--darkest;
    } //.request-item__title

    &__phone {
        display: block;
        margin-bottom: 5px;
        font-family: $f-num--b;
        font-size: 12px;
        color: $color-text;
    } //.request-item__phone

    &__service {
        display: block;
        margin-bottom: 5px;
        font-family: $f-i;
        font-size: 14px;
        line-height: 1;
        color: #969696;

        @include respond-to(xs) {
            @include text-overflow;
        }
    } //.request-item__service

    &__master {
        display: block;
        margin-bottom: 8px;
        font-size: 14px;
        line-height: 1;
        color: $gray--darken;

        @include respond-to(xs) {
            margin-bottom: 0;
            margin-right: 65px;
            @include text-overflow;
        }
    } //.request-item__master

    &__info {
        font-size: 14px;
        @include flex;
        align-items: center;
    } //.request-item__info

    &__price {
        font-family: $f-bold;
        font-size: 14px;
        color: $color-text;

        @include respond-to(xs) {
            position: absolute;
            right: 11px;
            bottom: 7px;
        }

        i {
            font-size: 12px;
        }
    } //.request-item__price

    &__date {
        margin-left: auto;
        font-family: $f-i;
        font-size: 13px;
        color: #969696;
        float: right;

        @include respond-to(xs) {
            padding: 5px 10px;
            font-size: 14px;
            color: $gray--darkest;
            text-align: center;
            background-color: transparent;
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            span {
                display: block;
                padding: 5px 0;

                &:first-child {
                    border-bottom: 1px solid $color-border--light;

                    span {
                        display: none;
                    }
                }
            }

            i {
                display: none;
            }
        }
    } //.request-item__date

    &__icon {
        position: absolute;
        top: 11px;
        right: 10px;
    } //.request-item__icon

    &.request-item--new {
        border-color: transparent;
        background-color: #f5f5f5;

        .request-item {

            &__icon {
                display: none;
            }
        }
    } //.request-item.request-item--new

    &.drag-sort {
        border: 0;
        box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.3);
    } //.request-item.drag-sort
} //.request-item
