.graph {
    background-color: #fff;

    &__date {
        margin-left: auto;

        @include respond-to(sm) {
            margin: 0 auto;
        }

        @include respond-to(xs) {
            margin: 0 auto 10px 0;
        }
    } //.graph__date

    &__sorting {

        @include respond-to(xs) {
            flex-direction: column;
        }

        .top-line {

            &__select {
                @include respond-to(sm) {
                    width: calc(50% - 10px);

                    .bb-input--select {
                        min-width: 100%;
                    }
                }

                @include respond-to(xs) {
                    width: 100%;
                }
            }
        }
    } //.graph__sorting

    .control-box {
        width: 100%;
        @include flexCenter;

        @include respond-to(xs) {
            order: 1;
        }
    } //.graph .control-box

    .select2-container {
        min-width: 300px;
    } //.graph .select2-container
} //.graph

.graph-table {
    @include flexColumn;

    table {
        width: 100%;
        border-collapse: collapse;
        table-layout: fixed;
        user-select: none;
    } //.graph table

    th,
    td {
        height: 53px;
        text-align: center;
        vertical-align: middle;
        border: 1px solid $color-border--lighten;
        cursor: default;
        min-width: 152px;

        @include respond-to(sm) {
            height: 48px;
        }
    }

    th {
        height: 45px;
        padding: 0;
        vertical-align: middle;
        text-transform: uppercase;
        font-size: 14px;
        text-align: center;
        font-family: $f-bold;
        font-weight: 500;
    } //.graph th

    &.is-empty {

        .graph-table {

            &__worker,
            &__hours {

                tbody {
                    display: none;
                }
            }
        }

        .empty {
            display: flex;
        }
    }

    &__inner {
        display: flex;
    } //.graph__inner

    &__worker {
        width: 20%;

        @include respond-min(sm) {
            flex-basis: 25%;
        }

        @include respond-to(sm) {
            width: 150px;
        }

        table {
            border: 1px solid $color-border--lighten;
        }

        tr,
        td {
            font-family: $f-bold;
            font-weight: 500;
            vertical-align: middle;
            text-align: center;

            @include respond-to(sm) {
                vertical-align: top;
            }
        }

        th,
        td {
            border-right: 0;
        }

        td {
            padding: 5px 10px;
        }

        th {

            a {
                color: $color-text;

                &:hover {
                    color: $gray--darkest;
                }
            }
        }
    } //.graph-table__worker

    &__hours {
        width: 80%;
        flex-grow: 1;
        flex-shrink: 1;

        @include respond-to(sm) {
            width: calc(100% - 150px);
            overflow-x: auto;

            table {
                table-layout: auto;
            }
        }

        th {
            text-transform: initial;
            letter-spacing: 0;
        } //.graph-table__hours th

        tr {

            td,
            th {

                &:first-child {
                    border-left: 0;
                }
            }
        }

        td {
            cursor: pointer;
            position: relative;

            &::after {
                content: '+';
                display: block;
                font-size: 2em;
                color: $gray--light;
                opacity: 0;
                transition: opacity 0.25s ease-in-out;
            }

            &:hover {

                &::after {
                    opacity: 1;
                }
            }
        } //.graph-table__hours td
    } //.graph-table__hours

    .user {
        position: relative;

        &__img {
            width: 40px;
            height: 40px;
            flex-shrink: 0;

            @include respond-to(sm) {
                display: none;
            }
        } //.graph-table user__img

        &__text {
            width: 100%;
            margin-top: 4px;
            line-height: 1.2;
            font-size: 14px;
            text-align: left;
            flex-grow: 1;
            @include text-overflow;
        } //.graph-table user__text

        &__name {
            font-size: 14px;
            @include text-overflow;

            @include respond-to(sm) {
                font-size: 13px;
            }
        } //.graph-table user__name

        &__time {
            font-family: $f;
            font-size: 12px;
            line-height: 1;
            text-align: center;
            border-radius: 15px;

            @include respond-to(sm) {
                text-align: left;
                color: $gray;
                background-color: transparent;
                padding: 0;
                top: auto;
                right: auto;
                bottom: -15px;
                left: 0;
            }
        } //.graph-table user__time
    } //.graph-table user

    .range {
        padding: 3px;

        &::after {
            display: none;
        }
    } //.graph-table .range

    .empty {
        display: none;
        padding-top: 35px;
    } //.graph-table .empty

    &--empty {
        @include flexColumn;

        td,
        th {
            height: 40px !important;
        } //.graph-table--empty td

        .empty {
            padding-top: 35px;
        } //.graph-table--empty .empty
    } //.graph-table--empty
} //.graph-table

.graph-item {
    padding: 3px 0;
    font-size: 14px;
    color: #fff;
    border-radius: $border-radius;
    background-color: #8a8a8a;

    &:hover {
        background-color: #a9a9a9;
    }

    & + & {
        margin-top: 3px;
    }

    @each $theme, $color in $text-colors {

        &.#{$theme} {
            background-color: $color;

            &:hover {
                background-color: lighten($color, 5%);
            }
        }
    }
} //.graph-item
