.modal {

    &-dialog {
        @include respond-to(xs) {
            margin-top: $base-indent * 3;
        }

        &--lg,
        &--md,
        &--sm {
            @include respond-to(sm) {
                margin: $base-indent auto;
            }
        }

        &--lg {
            max-width: 960px;
            margin: $base-indent auto;
        } //.modal-dialog--lg

        &--md {
            max-width: 768px;
            margin: $base-indent auto;
        } //.modal-dialog--md

        &--sm {
            max-width: 425px;
            margin: $base-indent auto;

            @include respond-to(xs) {
                margin: $base-indent 10px;
            }
        } //.modal-dialog--sm
    } //.modal-dialog

    &-body {

        .modal-close {
            top: 0;
            right: 0;
        }
    } //.modal-body

    &-header {
        color: #fff;
        border-bottom: none;
        background-color: $color-main;
        position: relative;

        &--transparent {
            color: $color-text;
            background-color: #fff;
            border-bottom: 1px solid $modal-border-color;

            .modal-title {
                font-family: $f-medium;
            }

            .modal-close {
                color: $color-text;
            }
        }

        &--light {
            color: $gray--darkest;
            border-bottom: 1px solid $modal-border-color;
            background-color: $color-bg--lighten;

            .modal-title {
                font-family: $f-medium;
                font-weight: 500;
            }
        }

        .modal__btn--close {
            top: 8px;
            right: 5px;
        }
    } //.modal-header

    &-title {
        margin: auto 0;
        font-family: $f;
        font-weight: 500;
        font-size: 17px;
        letter-spacing: 0;
        text-transform: initial;
        line-height: 1;

        &--center {
            margin: auto;
        }
    } //.modal-title

    &-footer {
        border-top: 1px solid $modal-border-color;
        border-bottom-left-radius: $modal-border-radius;
        border-bottom-right-radius: $modal-border-radius;
        background-color: $color-bg--lighten;

        button {
            padding: 4px 20px;
            font-size: 14px;
            box-shadow: none;

            &.btn-link {
                padding: 8px 20px 7px;
                transition: initial;

                &:hover {
                    text-decoration: none;
                    background-color: rgba($color-main, 0.2);
                }
            }

            &.btn--big {
                margin: auto;
                padding: 6px 30px;
                font-size: 15px;
            }
        }

        &--custom {
            padding: 0;
            border: 0;
            background-color: transparent;
            @include flexCenter;

            & > :not(:last-child) {
                margin-right: 0;
            }

            & > :not(:first-child) {
                margin-left: 0;
            }

            button {
                padding: 0;
                margin: 0;
                width: 50%;
                height: 50px;
                border: 0;
                border-radius: 0;
                box-shadow: none;
                font-size: 16px;

                &:first-child {
                    border-top: 1px solid $color-border--lightest;

                    &:hover {
                        color: darken($color-main, 15%);
                        border-left: 0;
                        border-right: 0;
                        border-bottom: 0;
                        background-color: transparent;
                    }
                }
            }
        } //.modal-footer--custom
    } //.modal-footer

    &-close {
        height: auto;
        padding: 5px 10px;
        font-size: 21px;
        font-weight: 600;
        line-height: 1;
        color: #fff;
        text-shadow: none;
        background-color: transparent;
        box-shadow: none;
        border: none;
        outline: none;
        cursor: pointer;
        opacity: 0.5;
        position: absolute;
        top: 7px;
        right: 5px;
        z-index: z(default);

        &:hover {
            text-decoration: none;
            opacity: 1;
        }

        &::before {
            content: '\f00d';
            @include fa('light');
            font-size: 16px;
        }

        &:not(:disabled):not(.disabled) {
            cursor: pointer;
        }

        &--dark {
            color: $gray;
        } //.modal-close--dark
    } //.modal-close
} //.modal

.modal-box {
    padding: 10px 15px 15px;
    border-bottom: 1px solid $color-border--lightest;
    position: relative;

    &:last-child {
        border-bottom: 0;
    }

    &__inner {
        @include flexColumn;
    } //.modal-box__inner

    &__title {
        display: inline-block;
        margin-bottom: 7px;
        font-size: 14px;
        color: $gray--light;
        flex-shrink: 0;
        position: relative;
    } //.modal-box__title

    &--title {
        padding-top: 0.6rem;
    } //.modal-box--title

    &--choice {
        padding: 0;

        .modal-box {

            &__inner {
                max-height: 450px;
                padding: 10px 0 15px;
                overflow-y: auto;

                @include respond-min(xs) {
                    @include bb-scroll;
                }
            } //.modal-box--choice .modal-box__inner

            &__title {
                margin-left: 0;
                padding: 0 15px;
                @include flexCenter;
                flex-wrap: wrap;

                .btn-link {
                    margin-left: auto;
                }

                .control-box {
                    width: 100%;
                }
            } //.modal-box--choice .modal-box__title
        }
    } //.modal-box--choice

    .datepicker--nav {
        position: absolute;
        top: -39px;
        right: -15px;
    } //.modal-box .datepicker--nav

    &__validate {
        margin: 0 15px 15px;
        text-align: center;
    } //.modal-box__validate
} //.modal-box

.triumph {

    &__inner {
        text-align: center;
    } //.triumph__inner

    &__title {
        display: block;
        margin-bottom: 13px;
        text-align: center;
        font-family: $f-bold;
        font-size: 20px;
        color: $gray--darkest;
    } //.triumph__title

    &__text {
        margin-bottom: $gutter;
        text-align: center;
    } //.triumph__text

    &__img {
        max-width: 220px;
        margin: auto auto $gutter * 2;
        background-color: #fbfbfb;

        @include respond-to(xs) {
            max-width: 90%;
        }
    } //.triumph__img

    &__suptitle {
        display: block;
        margin-bottom: 10px;
        color: $color-main;
    } //.triumph__subtitle

    &__desc {
        margin-bottom: $gutter * 2;
    } //.triumph__desc

    &__btn {
        margin-top: $gutter * 2;
    } //.triumph__btn

    .modal-body {
        padding: $gutter * 1.5;
    } //.triumph .modal-body

    .modal-content {
        background-color: #fbfbfb;
    } //.triumph .modal-content

    .modal-close {
        color: $color-text;
    } //.triumph .modal-close
} //.triumph

.m-album {
    min-width: 480px;

    @include respond-to(xs) {
        min-width: 320px;
    }

    &__item {
        margin-bottom: 30px;
        @include flexColumn;

        @include itemGutter(#{$gutter});
    } //.m-album__item

    &__inner {
        @include flexCenter;
        justify-content: space-between;
        @include respond-to(xs) {
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
        }

        .bb-input {

            &:last-child {
                margin-left: auto;
            }
        }
    } //.m-album__inner

    &__select {

        &:last-child {
            @include respond-to(xs) {
                margin-left: 0;
            }
        }
    } //.m-album__select

    &__title {
        margin-bottom: 5px;
        font-family: $f-medium;
        font-size: 14px;
    } //.m-album__title

    &__text {
        min-height: 110px;
        border-color: $color-border--light;
    } //.m-album__text

    .bb-input {
        @include respond-to(xs) {
            margin-bottom: 25px;
            width: 100%;

            &:last-child {
                margin-bottom: 0;
            }
        }

        &__input {
            width: 100%;
            height: 30px;
            line-height: 30px;
        }
    }

    .select2-container,
    .bb-select {
        min-width: 190px;
        max-width: 190px;
        @include respond-to(xs) {
            width: 100%;
            max-width: 100%;
            margin-bottom: 15px;

            &:last-child {
                margin-bottom: 0;
            }
        }

        &:last-child {
            margin-left: auto;
        }

        select {
            width: 100%;
            @include respond-to(xs) {
                width: calc(100% - 10px);
            }
        }
    }

    .bb-select {

        &::before {
            right: 7px;
        }
    }
} //m-album

.add {
    // position: relative;

    &__user {
        @include vx;
        top: -50px;
        z-index: z(fixed);
    } //.add__user

    &__btn {
        margin: auto;
        padding: 7px 40px;
        font-size: 15px;
    } //.add__btn

    .user {

        &__avatar {
            margin: 0;
        }

        &__img {
            width: 100px;
            height: 100px;
        }
    } //.add .user

    .profile-editor {

        &__item {
            margin-bottom: 20px;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;
        }

        &__label {
            width: 100%;
            margin-right: 0;
            margin-bottom: 7px;
            text-align: left;
        }

        &__input {
            width: 100%;
        }
    } //.add .profile-editor

    .info-block {
        top: 2px;
    } //.add .info-block

    .modal-body {
        padding: 70px 30px 0;
    } //.add .modal-body
} //.add

.dell {

    &__text {
        padding: $gutter 0;
    } //.dell__text
} //.dell

.add-avatar {
    text-align: center;

    &__inner {
        padding: $gutter * 2.5 $gutter 0;
    } //.add-avatar__inner

    &__title {
        margin-bottom: $gutter;
        font-family: $f-medium;
        font-size: 20px;
    } //.add-avatar__title

    &__image {
        margin-top: $gutter * 2;
    } //.add__image

    &__text {
        margin-bottom: $gutter;
        font-size: 14px;
    } //.add__text

    &__img {
        width: calc(100% - 120px);
        margin: 0 auto $gutter;
        overflow: hidden;
    } //.add__img

    &__preview {
        width: 100px;
        height: 100px;
        margin: auto;
        overflow: hidden;

        & > div {
            border-radius: 50%;
            width: 100% !important;
            height: 100% !important;
        }
    } //.add__preview

    &__btn {
        padding: 7px 30px;
        font-size: 15px;
    } //.add-avatar__btn

    .modal-body {
        text-align: center;
    } //.add-avatar .modal-body
} //.add-avatar

#add-master {

    .add-avatar {
        @include flexColumn;
        @include coverdiv;
        z-index: z(default);
        background-color: #fff;
        display: none;

        &__inner {
            flex-grow: 1;
        }
    }

    .modal-dialog {
        margin-top: 60px;
    }

    .modal-body {
        padding: 60px 30px 15px;
    }

    .profile-editor__select + .profile-editor__select {
        margin-left: 15px;

        @include respond-to(xs) {
            margin-left: 12px;
        }
    } // #add-master .profile-editor__select + .profile-editor__select
} // #add-master

.modal-choice {

    &__checkbox {

        .bb-checkbox {

            &__title {
                font-family: $f-medium;
                font-size: 16px;
            }
        }
    }

    .bb-checkbox {

        &__wrap {
            margin: 15px 0;
            @include flexCenter;
            justify-content: center;

            .bb-checkbox {

                & + .bb-checkbox {
                    margin-left: $gutter * 2;
                }
            }
        }

        .button-box {
            margin: 15px 0;
            display: flex;
            justify-content: center;

            .btn {
                min-width: 150px;

                @include respond-to(i5) {
                    max-width: 125px;
                }

                & + .btn {
                    margin-left: $gutter;
                }
            }
        }
    } //.modal-choice
} //.modal-choice__checkbox

.shedule-picker {

    .modal-body {
        padding: 0;
    } //.shedule-picker .modal-body

    .datepicker-inline {
        display: block;
        position: static;
        transform: translate(0);
        opacity: 1;
    } //.shedule-picker .datepicker-inline

    .bb-checkbox {
        padding: 10px 10px 10px 40px;

        &:hover {
            background-color: $color-bg--lighten;
        }

        &__box {
            top: 10px;
            left: 15px;
        }
    } //.shedule-picker .bb-checkbox

    .user {

        &__name {
            color: $gray--darkest;
            // font-family: $f-medium;
        } //.shedule-picker .user__name
    } //.shedule-picker .user

    &--master {

        .user {

            &__name {
                font-family: $f-medium;
            } //.shedule-picker .user__name
        } //.shedule-picker .user

        .bb-input {
            width: 100%;
            margin-top: 10px;

            input {
                height: 40px;
                background-color: $color-bg--light;
            }
        }
    } //.shedule-picker--master
} //.shedule-picker
