.bb-pushUp {
    display: block;
    padding: 5px 25px 50px 15px;
    font-size: 15px;
    line-height: 1.2;
    color: #fff;
    background-color: $color-accent;
    overflow: hidden;
    text-overflow: ellipsis;
    position: fixed;
    top: 10px;
    right: -200%;
    max-width: 310px;
    width: 100%;
    height: 50px;
    border-radius: $border-radius;
    box-shadow: $bxs-box;
    z-index: z(modal);
    @include tr;

    @include respond-to(i5) {
        max-width: 300px;
    }

    &__close {
        width: 10px;
        height: 10px;
        fill: #fff;
        cursor: pointer;
        position: absolute;
        top: 4px;
        right: 7px;
    }

    &.is-active {
        right: 10px;
    }

    &.is-success {
        background-color: $color-success;
    }

    &.is-error {
        background-color: $color-danger;
    }
} //bb-pushUp
