.move-block {
    @include flexColumn;
    @include visibleBlock;

    &__header {
        height: 50px;
        padding: 0 10px;
        font-size: 18px;
        color: $gray--darkest;
        @include border(bottom);
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        @include flexCenter;
        justify-content: center;

        .select2-container--default {

            &.select2-container--open {

                .select2-selection--single {
                    border-bottom: 1px solid;
                }
            }

            .select2-selection--single {
                border-bottom: 0;

                .select2-selection__arrow b {
                    top: -1px;
                }

                .select2-selection__rendered {
                    font-family: $f;
                    font-size: 17px;
                }
            }
        }
    } //.move-block__header

    &__body {
        background-color: $color-bg--lighten;
        overflow-x: hidden;
        @include bb-scroll--thin;
        flex-grow: 1;
        position: absolute;
        top: 50px;
        bottom: 0;
        left: 0;
        right: 0;
    } //.move-block__body

    &__footer {
        padding: 10px;
        // background-color: $color-bg--lightest;
        background-color: #fff;
        @include border(top);
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        @include flexColumn;
        align-items: center;

        .text {
            margin-bottom: 10px;
        }

        .button-box {
            width: 100%;
            @include flexCenter;
            justify-content: center;
        }

        .btn {
            width: 50%;
            padding-left: 15px;
            padding-right: 15px;

            & + .btn {
                margin-left: 10px;
            }
        }
    } //.move-block__footer

    &__close {
        width: 38px;
        height: 38px;
        padding: 10px;
        cursor: pointer;
        @include tr;
        @include vy;
        right: 0;

        &:hover {
            fill: $gray--darkest;
        }
    } //.move-block__close

    &__inner {
        background-color: #fff;
    } //.move-block__inner

    &__item {
        min-height: 50px;
        margin: 5px 0;

        &--btn {
            font-family: $f-medium;
            font-size: 16px;
            cursor: pointer;
        }
    } //.move-block__item

    &__box {
        background-color: #fff;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        @include visibleBlock;
    } //.move-block__box

    &__icon {
        width: 22px;
        height: 22px;
        margin-right: 10px;
    } //.move-block__icon

    &__date {
        font-family: $f-medium;
    } //.move-block__date

    &--mobile {

        .user {

            &__phone {
                color: $gray--light;
            }
        } //.move-block .user

        .aplication-item {
            margin-top: $gutter / 2;
            padding: 15px 10px;
            box-shadow: none;
            @include border(top);
            @include border(bottom);

            &__row {
                margin-bottom: 15px;
                justify-content: center;
            }

            &__col {
                @include size-sm(12);

                & + .aplication-item__col {
                    margin-top: 15px;
                }
            }

            &__service {
                margin-bottom: 0;

                .aplication-item__col + .aplication-item__col {
                    margin-top: 15px;
                }
            }

            &__btn {

                &--edit {
                    margin: auto;

                    @include respond-to(xs) {
                        padding: 5px 0;
                        font-size: 14px;
                    }
                } //.move-block .aplication-item__btn--edit
            } //.move-block .aplication-item__btn
        } //.move-block .aplication-item

        .aplication-info {
            padding: 15px 10px;

            &__wrap {
                overflow-y: auto;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 123px;
            }
        } //.move-block .aplication-info

        .search-client {
            display: block;
            height: 100%;
            background-color: #fff;

            &__form {
                padding: 10px;
            }

            .btn {
                display: block;
                width: 100%;
                padding-top: 12px;
                padding-bottom: 9px;
                font-size: 17px;
                background-color: #fff;
            } //.move-block .search-client .btn

            .user {
                padding: 10px;
            } //.move-block .search-client .user

            .aplication__in {
                background-color: #fff;
                position: absolute;
                top: 111px;
                left: 0;
                right: 0;
                bottom: 0;
            } //.move-block .search-client .aplication__in
        } //.move-block .search-client

        .new-client {
            z-index: inherit;
            @include visibleBlock;

            &__form {
                min-height: 100%;
                padding-top: 0;
                background-color: #fff;
            } //.move-block .new-client__form

            .bb-input {

                &--light {
                    margin-bottom: 5px;
                }
            } //.move-block .new-client .bb-input

            .bb-text {
                margin-bottom: 0;
            } //.move-block .new-client .bb-text

            .move-block {

                &__body {
                    background-color: #fff;
                } //.move-block .new-client .move-block__body
            } //.move-block .new-client .move-block
        } //.move-block .new-client

        .aplication-success {
            position: inherit;

            &__user {
                margin-bottom: 5px;
                padding: 10px;
                background-color: #fff;
            } // .aplication-success__user

            &__tab {
                padding: 10px;
            } // .aplication-success__tab

            .tab {
                background-color: #fff;

                &__content {
                    background-color: #fff;
                    @include border(top);
                    @include border(bottom);
                    top: 125px;
                    bottom: auto;

                    &#aplication-chat {
                        bottom: 0;
                        border-bottom: 0;
                    }

                    &#aplication-history,
                    &#aplication-info {
                        height: 100%;
                    }

                    &#aplication-history {

                        .box-inline__wrap {
                            padding: 15px 10px;
                            overflow-y: auto;
                            position: absolute;
                            top: 0;
                            left: 0;
                            right: 0;
                            bottom: 123px;
                        }
                    }
                }

                &__inner {
                    padding: 15px 10px;
                }

                &__link {
                    padding: 5px 0 11px;
                }
            } // .aplication-success .tab

            .move-block {

                &__body {
                    overflow: hidden;
                } //.aplication-success .move-block__body
            } //.aplication-success .move-block
        } //.move-block .aplication-success

        .aplication-comments {

            .move-block {

                &__body {
                    background-color: #fff;
                }

                &__inner {
                    padding: 20px 10px;
                }
            }
        } //.aplication-success

        .bb-date {
            width: 100%;
            margin: 0;

            &__title {
                margin-bottom: 0;
            }

            &__input {
                font-size: 16px;

                &:not(.move-block__date) {
                    font-family: $f;
                }

                &:focus {
                    box-shadow: none;
                }
            }
        } //.aplication-success .bb-date

        .select2-container {

            .select2-selection--single {
                height: 37px;
                line-height: 37px;

                .select2-selection__rendered {
                    padding-left: 0;
                    padding-right: 25px;
                    line-height: 37px;
                    font-size: 16px;

                    .custom-select__results {

                        span {

                            &:first-child {
                                @include text-overflow;
                                flex-grow: 1;
                            }

                            &:not(:first-child) {
                                font-size: 14px;
                            }

                            &:nth-child(2) {
                                margin-left: auto;
                            }

                            &:nth-child(3) {
                                margin-right: 5px;
                            }
                        }
                    }
                }
            }
        } //.aplication-success .select2-container

        .select2-container--default {

            &.select2-container--open,
            &.select2-container--open.select2-container--below {

                .select2-selection--single {
                    border-color: transparent;
                    border-bottom-color: $color-main;

                    &:hover {
                        border-color: transparent;
                        border-bottom-color: $color-main;
                    }
                }
            }

            .select2-selection--single {
                border-left: 0;
                border-right: 0;
                border-top: 0;
                border-radius: 0;
                box-shadow: none;
            }
        } //.aplication-success .select2-container--default
    } //.move-block--mobile
} //.move-block

.move-block-item {
    padding: 10px;
    background-color: #fff;

    @include border(top);
    @include border(bottom);
    @include flexCenter;

    &__client {
        @include flexCenter;
        flex-grow: 1;

        &--selected {
            flex-grow: 1;
        }
    } //.move-block-item__client

    &__icon {
        margin-left: auto;
        font-size: 18px;
    } //.move-block-item__icon

    &--date {
        padding: 0;

        input {
            height: auto;
            padding: 13px;
            border: 0;
            background-color: transparent;
        }

        i {
            margin-right: 10px;
        }
    } // .move-block-item--date
} // .move-block-item

.move-block-service {
    @include flexCenter;

    &__info {
        @include flexColumn;
        flex-grow: 1;
    } //.move-block-service__info

    &__time {
        font-size: 14px;
    } //.move-block-service__time

    &__title {
        font-family: $f-medium;
        color: $gray--darken;
    } //.move-block-service__title

    &__duration {

        .text-time {
            font-size: 14px;

            i {
                margin-right: 5px;
                color: $color-icon--light;
            }//.move-block-service__duration .text-time i
        }//.move-block-service__duration .text-time

        .text-master {
            font-family: $f-i;
            font-size: 15px;
        }//.move-block-service__duration .text-master
    } //.move-block-service__duration

    &__price {

        i {
            margin-left: 5px;
            font-size: 14px;
            color: $color-icon--light;
        }//.move-block-service__price i
    } //.move-block-service__price
} //.move-block-service

.move-select {
    background-color: #fff;
    @include tr;
    @include flexColumn;

    &.is-active {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: z(overlay);

        .move-select {

            &__body,
            &__footer {
                display: flex;
            }

            &__search {

                &::after {
                    display: none;
                }

                i {
                    display: block;
                    padding: 10px;
                    left: 0;
                }
            }

            &__field {
                height: auto;
                padding: 15px 25px 15px 35px;
                @include border(bottom);
            }
        }
    } //.move-select.is-active

    &__search {
        position: relative;

        &::after {
            content: '\f107';
            @include fa('light');
            color: $color-main;
            @include vy;
            right: 10px;
        }

        i {
            display: none;
            @include vy;
            left: 10px;
        }
    } //.move-select__search

    &__field {
        width: 100%;
        height: 37px;
        padding: 0;
        font-size: 16px;
        line-height: 1;
        border-left: 0;
        border-right: 0;
        border-top: 0;
        border-radius: 0 !important;
        box-shadow: none;

        @include placeholder {
            padding: 0;
        }

        &:focus,
        &:hover {
            border-left: 0;
            border-right: 0;
            border-top: 0;
            box-shadow: none;
            border-radius: 0;
        }
    } //.move-select__field

    &__body {
        display: none;
        background-color: #fff;
        @include bb-scroll--thin;
        position: absolute;
        top: 50px;
        left: 0;
        right: 0;
        bottom: 60px;
        flex-direction: column;
        flex-grow: 1;
    } //.move-select__body

    &__footer {
        display: none;
        height: 60px;
        @include border(top);
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;

        .btn {
            // display: inherit;
            margin: auto;
        }
    } //.move-select__footer

    &__results {
        background-color: #fff;
    } //.move-select__result

    &__result {
        padding: 7px 10px;

        & + & {
            @include border(top);
        }

        &.is-selected {
            background-color: $color-bg--lighten;
        }

        .user {

            &__img {
                width: 40px;
                height: 40px;
            }
        }
    } //.move-select__result
} //.move-select

.item-info {

    &__title {
        margin-bottom: 5px;
        color: $gray--darkest;
        font-family: $f-medium;
        font-size: 15px;

        span {
            position: relative;

            & + span {

                &::before {
                    margin-right: 5px;
                    margin-left: 1px;
                    content: '\f067';
                    @include fa('regular');
                    font-size: 11px;
                    color: $color-main;
                }
            }
        }
    } //.item-info__title

    &__desc {
        font-size: 14px;
        @include flexCenter;
    } //.item-info__desc

    &__time {
        flex-grow: 1;

        i {
            color: $color-main;
        }
    } //.item-info__time

    &__price {

        i {
            font-size: 12px;
            color: $color-main;
        } //.item-info__price i
    } //.item-info__price
} //.item-info
